import React from "react";
import { Card, Image } from 'semantic-ui-react'
import config from "../../../config";


const ItemCard = ({ item, marign = 0 }) => {
    const url = config.url
    const style = {
        margin: `${marign}px`
    }

    const getExtraContent = () => {
        switch (item.type) {
            case 'hero-item':
                return (
                    <Card.Content extra>
                        Hero: {item.hero}
                    </Card.Content>
                )
            case 'terrain':
                return (
                    <Card.Content extra>
                        Terrain
                    </Card.Content>
                )
            case 'creeps':
                return (
                    <Card.Content extra>
                        Side: {item.side}
                    </Card.Content>
                )
            default: return null
        }
    }
    return (
        <Card style={style} >
            <Image src={url+item.src} wrapped ui={false} />
            <Card.Content>
                <Card.Header>
                    Rarity: <span className={item.rarity}>{item.rarity}</span>
                </Card.Header>
                <Card.Description>
                    Name: {item.name}
                </Card.Description>
            </Card.Content>
            {getExtraContent()}
        </Card>
    )
}

export default ItemCard