import React, { useEffect } from "react";
import {
    Button,
    Grid,
    Image,
    Input
} from "semantic-ui-react"
import { useTranslation } from 'react-i18next';
import '../../../../styles/payment_page.css'
import '../../input.css'
import config from "../../../../config";
import * as api from '../../../../api/index'
import PayForRentErrorMsg from './PayForRentErrorMsg.jsx'


const PayForRentMobile = (props) => {
    const {
        costPerHour, trophyLevel, behavior, rating, makePaymentRequest,
        promo, setPromo, hoursAmount, setHoursAmount, coef, setCoef,
        srcScrProfile, srcScrMmr,
        loading, error, errorCode, errorBottom
    } = props.data

    const { t } = useTranslation()

    useEffect(() => {
        api.getPromoCoef(promo, (err, res) => {
            if (err) {
                console.log(err);
            } else
                setCoef(res)
        })
    }, [promo])


    const sum = Math.round(coef * costPerHour * hoursAmount * 1000) / 1000

    return (<>
        <Image style={{
            width: '80vw',
            marginLeft: 'auto',
            marginRight: 'auto',
            marginTop: '3vh',
        }} src={srcScrProfile} />

        <Image style={{
            width: '80vw',
            marginLeft: 'auto',
            marginRight: 'auto',
            marginTop: '1vh',
        }} src={srcScrMmr} />

        <Grid divided style={{ fontSize: "120%" }}>
            <Grid.Row textAlign="center" columns={2} >
                <Grid.Column className='column-top'>
                    {t("payment-page.rating")}: {rating}
                </Grid.Column>
                <Grid.Column className='column-top'>
                    {t("payment-page.behavior")}: {behavior}
                </Grid.Column>
            </Grid.Row>

            <Grid.Row textAlign="center" columns={2}>
                <Grid.Column className='column-top'>
                    {t("payment-page.trophy_lvl")}: <br />{trophyLevel}
                </Grid.Column>
                <Grid.Column>
                    <div>{t("payment-page.hours")}: {hoursAmount} </div>
                    <input
                        className="input-type-1"
                        min={config.availableHours.min}
                        max={config.availableHours.max}
                        onChange={(e) => { setHoursAmount(e.target.value) }}
                        type='range'
                        value={hoursAmount}
                        style={{ width: '70%' }}
                    />
                </Grid.Column>
            </Grid.Row>

            <Grid.Row>
                <Grid.Column textAlign="center">
                    <Input placeholder={t("payment-page.promo")} value={promo}
                        onChange={(e) => {
                            setPromo(e.target.value)
                        }} />
                </Grid.Column>
            </Grid.Row>

            <Grid.Row>
                <Grid.Column textAlign="center">
                    {t("payment-page.total")}: {sum} {t("currency")}
                </Grid.Column>
            </Grid.Row>

            <Grid.Row>
                <Grid.Column className="column-with-button">
                    <Button loading={loading} positive onClick={makePaymentRequest}
                        content={t("payment-page.go_to_payment_page")} />
                </Grid.Column>
            </Grid.Row>
            <PayForRentErrorMsg
                error={error}
                errorCode={errorCode}
                errorBottom={errorBottom}
                isLinkOnBottom={errorCode !== 406}
            />
        </Grid>
    </>
    )
}

export default PayForRentMobile