import React, { useState } from "react";
import { useDispatch } from "react-redux";
import { useTranslation } from 'react-i18next';

import { setAlert } from "../../redux/alertSlice";
import config from "../../config";

import * as api from '../../api/index'
import LRFRender from './LRFRender.jsx'
import { setRole } from "../../redux/roleSlice";

const LoginRegisterForm = () => {
    const [email, setEmail] = useState('')
    const [password, setPassword] = useState('')
    const [isResetPWClicked, setIsResetPWClicked] = useState(false)
    const [loadingRegister, setLoadingRegister] = useState(false)
    const [loadingLogin, setLoadingLogin] = useState(false)
    const [isError, setIsError] = useState(false)
    const [errorMsg, setErrorMsg] = useState('')
    const [agreeTerms, setAgreeTerms] = useState(false)
    const dispatch = useDispatch()
    const { t } = useTranslation()

    const displayAlert = (isBlue = true, text = 'placeholder', timeOutMS = 4000) => {
        dispatch(setAlert({
            alertVisiable: true, isAlertColorBlue: isBlue,
            alertMessage: text
        }))
        setTimeout(() => {
            dispatch(setAlert({
                alertVisiable: false, isAlertColorBlue: isBlue,
                alertMessage: text
            }))
        }, timeOutMS)
    }

    const toggleVisible = () => {
        setIsError(false)
        setIsResetPWClicked(true)
    }

    const setClientRole = () => {
        api.emailConfirmed(localStorage.getItem('token'), (err, res) => {
            if (res.data === 'email confirmed')
                dispatch(setRole('auth-not-made-order'))
            else dispatch(setRole('auth-not-confirmed'))
        })
    }

    const validateEmail = (email) => {
        let regexEmail = /^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/;
        if (email.match(regexEmail)) {
            return true;
        } else {
            return false;
        }
    }

    const resetPassword = () => {
        if (validateEmail(email)) {
            api.requestChangePW(email, (err, res) => {
                console.log('requestChangePW err, res:', err, res);
                if (err) throw err
                displayAlert(true, t('alertMessages.email-change-pw-send-success'), config.alertTimeOuts.medium)
            })
        } else {
            setIsError(true)
            setErrorMsg(t('alertMessages.errors.invalidEmail'))
        }
    }

    const validatePassword = (pw) => {
        return pw.split('').length >= 6
    }

    const login = () => {
        api.login({ email, password }, (err, res) => {
            switch (err.message) {
                case 'Network Error':
                    setLoadingLogin(false)
                    break
                case 'Request failed with status code 401':
                    setLoadingLogin(false)
                    setErrorMsg('wrong password or email')
                    setIsError(true)
                    break
                case false:
                    break
                default:
                    setLoadingLogin(false)
                    break
            }
            if (res.status === 200) {
                setLoadingLogin(false)
                displayAlert(true, t('alertMessages.userLoggedIn'), config.alertTimeOuts.small)
                localStorage.setItem('token', res.data.token)
                setClientRole()
            } else console.log('ushandeled res status:', res.status);
        })
    }

    const handleLogin = () => {
        if (validateEmail(email)) {
            if (validatePassword(password)) {
                setLoadingLogin(true)
                login()
            } else {
                setIsError(true)
                setErrorMsg(t('alertMessages.errors.pw-not-have-6-symbols'))
                setLoadingLogin(false)
            }
        } else {
            setLoadingLogin(false)
            setIsError(true)
            setErrorMsg(t('alertMessages.errors.invalidEmail'))
        }
    }

    const register = () => {
        api.register({ email, password }, (err, res) => {
            switch (err.message) {
                case false:
                    break
                case 'Network Error':
                    setIsError(true)
                    setErrorMsg(t('alertMessages.errors.network'))
                    setLoadingRegister(false)
                    break
                case 'Request failed with status code 403':
                    setIsError(true)
                    setErrorMsg(t('alertMessages.errors.userExists'))
                    setLoadingRegister(false)
                    break
                default:
                    break
            }
            switch (res.status) {
                case undefined:
                case false:
                    break
                case 200:
                    setLoadingRegister(false)
                    displayAlert(true, t('alertMessages.userRegistered'), config.alertTimeOuts.small)
                    localStorage.setItem('token', res.data.token)
                    setClientRole()
                    break
                default:
                    break
            }
        })
    }

    const handleRegister = () => {
        if (validateEmail(email)) {
            if (validatePassword(password)) {
                if (!agreeTerms) {
                    setIsError(true)
                    setErrorMsg(t('personal-space.agree-terms'))
                } else {
                    register()
                    setLoadingRegister(true)
                }
            } else {
                setLoadingRegister(false)
                setIsError(true)
                setErrorMsg(t('alertMessages.errors.pw-not-have-6-symbols'))
            }
        } else {
            setLoadingRegister(false)
            setIsError(true)
            setErrorMsg(t('alertMessages.errors.invalidEmail'))
        }
    }

    return (
        <LRFRender email={email} setEmail={setEmail} password={password} isError={isError} setIsError={setIsError}
            setPassword={setPassword} handleLogin={handleLogin} handleRegister={handleRegister}
            isResetPWClicked={isResetPWClicked} resetPassword={resetPassword} errorMsg={errorMsg}
            toggleVisible={toggleVisible} loadingLogin={loadingLogin} loadingRegister={loadingRegister}
            agreeTerms={agreeTerms}
            setAgreeTerms={setAgreeTerms} />
    )
}

export default LoginRegisterForm