import React from 'react'
import { Label } from 'semantic-ui-react'


/**
 * Renders array of tags objects: {color: String, name: String}
 * Receives as props (all optionals): 
 * { 
 *  tags: arrayOfTagsObjects, 
 *  style: parentDivStyle, 
 *  limit: limitTagsCount, 
 *  size: labelSize 
 * }
 */
const AccountTags = ({ tags = [], style = {}, limit = 88, size = 'medium',
    accountTagsAvailable = false, tagsStyle = {} }) => {
    const toggleTag = (name) =>
        document.getElementById(name).click()

    const renderTags = () =>
        tags
            .filter((value, index) => index < limit) // limit
            .map(tag => ( // Render
                <>
                    {
                        tag.name
                            ? <Label
                                style={
                                    accountTagsAvailable ?
                                        { cursor: 'pointer', ...tagsStyle } : { ...tagsStyle }
                                }
                                onClick={
                                    accountTagsAvailable ?
                                        () => toggleTag(tag.name) : () => { }}
                                color={tag.color}
                                content={tag.name}
                                size={size} />
                            : <div></div>
                    }
                </>
            ))

    return (
        <div style={style}>
            {renderTags()}
        </div>
    )
}

export default AccountTags