import React from "react";
import {
    Menu,
    Icon,
    Dropdown,
    Container,
    Image,
} from 'semantic-ui-react'
import config from "../../config";
import { useTranslation } from "react-i18next";
import {
    Link
} from 'react-router-dom'


const MobileHeader = () => {
    const { t } = useTranslation()

    const dropdownItem = (icon, link, nameBtn, disabled = false) =>
        <Dropdown.Item disabled={disabled} >
            <Link to={link} >
                <Icon name={icon} color={config.headerButtonColor} />
                {nameBtn}
            </Link>
        </Dropdown.Item>

    return (
        <Menu style={{ backgroundColor: '#0E1428', paddingTop: '.8vh' }}>
            <Container>
                <Menu.Item as='a' header>
                    <Image size='mini' src='/logo192.png' style={{ marginRight: '1.5em' }} />
                    <Link to={config.pages.home}>
                        {config.projectName}
                    </Link>
                </Menu.Item>

                <Dropdown item simple text='Menu' style={{ color: 'white' }}>
                    <Dropdown.Menu>
                        {dropdownItem('home', config.pages.home, t('header.home'))}
                        {dropdownItem('shopping cart', config.pages.accounts, t('header.accounts'))}
                        {dropdownItem('user outline', config.pages.personalSpace, t('header.personal-space'))}
                        {dropdownItem('money bill alternate outline', config.pages.balance, t('header.balance'))}
                        {dropdownItem('crosshairs', config.pages.streamSnipe, t('header.stream-snipe'), true)}
                        {dropdownItem('trophy', config.pages.battleCup, t('header.battle-cup'), true)}
                    </Dropdown.Menu>
                </Dropdown>
            </Container>
        </Menu>
    )
}

export default MobileHeader