import React from "react";
import { useTranslation } from 'react-i18next';
import { Link } from "react-router-dom";
import { Button, Divider, Grid, Header, Segment } from "semantic-ui-react";
import config from "../config";

const Footer = ({ isMobile }) => {
    const { t } = useTranslation()
    const footerItem = {
        paddingTop: '1vh',
        padding: '1vh',
    }

    const segmentStyle = {
        width: '100%',
        minHeight: '8em',
        backgroundColor: '#0E1428',
        fontSize: '1.3em',
        color: 'wheat'
    }

    const getOSName = () => {
        // This script sets OSName variable as follows:
        // "Windows"    for all versions of Windows
        // "MacOS"      for all versions of Macintosh OS
        // "Linux"      for all versions of Linux
        // "UNIX"       for all other UNIX flavors 
        // "Unknown OS" indicates failure to detect the OS

        var OSName = "Unknown OS";
        if (navigator.appVersion.indexOf("Win") != -1) OSName = "Windows";
        if (navigator.appVersion.indexOf("Mac") != -1) OSName = "MacOS";
        if (navigator.appVersion.indexOf("X11") != -1) OSName = "UNIX";
        if (navigator.appVersion.indexOf("Linux") != -1) OSName = "Linux";

        console.log('Your OS: ' + OSName);
        return OSName
    }

    const renderMadeIn = () => {
        const osName = getOSName()
        const canUnderstandFlagEmoji = osName === "UNIX" || osName === 'MacOS' || osName === 'Linux'
        return canUnderstandFlagEmoji
            ? <div style={{ padding: '1em' }}>Made in 🇩🇪 with ❤️</div>
            : <div style={{ padding: '1em' }}>Made in Germany with ❤️</div>
    }

    const leftColumn = () => (
        <>
            <Link to={config.pages.termsOfUse}>
                <Button content={t('footer.terms-of-use')} inverted color='grey' />
            </Link>
            {renderMadeIn()}
        </>
    )

    const problemsEmail = () => (
        <Header as='h3' style={{ color: 'wheat' }} >
            {t("footer.problems")}
            <Header.Subheader style={{ color: 'wheat' }}>
                {config.problemsEmail}
            </Header.Subheader>
        </Header>
    )

    const questionsEmail = () => (
        <Header as='h3' style={{ color: 'wheat' }} >
            {t("footer.questions")}
            <Header.Subheader style={{ color: 'wheat' }}>
                {config.contactEmail}
            </Header.Subheader>
        </Header>
    )

    const mobileFooter = (jsxChildFunc) => (
        <div style={footerItem}>
            {jsxChildFunc()}
        </div>
    )


    const propOfValve = () => (<>{t('footer.property-of-valve')}</>)

    return isMobile ?
        <Segment inverted vertical style={segmentStyle}>
            {mobileFooter(leftColumn)}
            <Divider />
            {mobileFooter(propOfValve)}
            <Divider />
            {mobileFooter(problemsEmail)}
            <Divider />
            {mobileFooter(questionsEmail)}
            <Divider />
        </Segment>
        :
        <Segment inverted vertical style={segmentStyle}>
            <Grid columns={3}>
                <Grid.Row>
                    <Grid.Column textAlign='center'>
                        {leftColumn()}
                    </Grid.Column>
                    <Grid.Column textAlign='center' style={{ fontSize: '0.8em' }} >
                        {propOfValve()}
                    </Grid.Column>
                    <Grid.Column textAlign='center'>
                        {problemsEmail()}
                        {questionsEmail()}
                    </Grid.Column>
                </Grid.Row>
            </Grid>
        </Segment >
}

export default Footer