import React from "react";
import { useSelector } from "react-redux";
import { Transition } from 'semantic-ui-react'
import '../styles/alert.css'
import { isMobile } from 'react-device-detect';


const AlertToReturn = () => {
    const alert = useSelector(state => state.alert.alert)

    const alertStyle = {
        right: isMobile ? '10px' : '110px',
        width: isMobile ? '80vw' : '350px'
    }
    const zIndex = {
        zIndex: '1001'
    }
    const renderAlert = () => {
        return (
            <Transition visible={alert.alertVisiable}
                animation='fade' style={zIndex}
                duration={alert.alertVisiable ? 500 : 1000}>
                <div>
                    <div className='message'
                        id={alert.isAlertColorBlue ?
                            'message-blue' : 'message-red'}
                        style={alertStyle}>
                        {alert.alertMessage}
                    </div>
                </div>
            </Transition >
        )
    }

    return (
        renderAlert()
    )
}

export default AlertToReturn