import React from "react";
import {
    HashRouter as Router,
    Switch,
    Route
} from "react-router-dom";
import { isMobile } from 'react-device-detect';
import Cookies from "./сomponents/Cookies";
import config from './config'
import Header from './сomponents/Header/'
import Home from './pages/Home_Page'
import Accounts from './pages/Accounts'
import PersonalSpace from './pages/Personal_Space/'
import TermsOfUse from './pages/TermsOfUse'
import Footer from './сomponents/Footer'
import Balance from './pages/Balance'
import SuccessfulPayment from "./pages/SuccessfulPayment";
import ConfirmPage from './pages/Confirmation/ConfirmPage'
import ConfirmOrder from './pages/Confirmation/ConfirmOrder'
import ChangePassword from "./pages/ChangePassword";
import Alert from './сomponents/Alert'
import CheckoutFailed from './pages/CheckoutFailed'
import NotFound from './pages/NotFound'
import BattleCup from "./pages/BattleCup";
import back from "./img/background.jpg"
import ReactGA from 'react-ga'

ReactGA.initialize('UA-219618497-1')


const App = () => {
    const containerDivStyle = {
        backgroundImage: `url(${back})`,
        backgroundSize: "cover",
    }


    return (
        <div style={containerDivStyle}>
            <Router>
                <Alert />
                <Header isMobile={isMobile} />
                <Switch>
                    <Route exact path={config.pages.home} component={Home} />
                    <Route exact path={config.pages.checkout_failed} component={CheckoutFailed} />
                    <Route exact path={config.pages.accounts} component={Accounts} />
                    <Route exact path={config.pages.personalSpace} component={PersonalSpace} />
                    <Route exact path={config.pages.confirm} component={ConfirmPage} />
                    <Route exact path={config.pages.successful} component={ConfirmOrder} />
                    <Route exact path={config.pages.changePassword} component={ChangePassword} />
                    <Route exact path={config.pages.battleCup} component={BattleCup} />
                    <Route exact path={config.pages.termsOfUse} component={TermsOfUse} />
                    <Route exact path={config.pages.balance} component={Balance} />
                    <Route exact path={config.pages.successfulPayment} component={SuccessfulPayment} />
                    <Route component={NotFound} />
                </Switch>
                <Footer isMobile={isMobile} />
                <Cookies />
            </Router>
        </div >
    )
}

export default App