import React, { useState } from "react"
import {
    Transition,
    Grid,
    Button,
    Segment,
    Image,
    GridRow,
    Statistic
} from 'semantic-ui-react'
import { useTranslation } from 'react-i18next';
import Timer from './Timer'
import config from "../../config";
import { isMobile } from 'react-device-detect';
//TODO: разница в ммр отправить в конфиг
import * as utils from '../../utils'
import * as api from '../../api/index'
import ProlongModal from "./ProlongModal";


const Order = ({ mmr, deadline, login, password, orderId, accountId }) => {
    const { t } = useTranslation()

    const [timeLeft] = useState((new Date(deadline)).getTime() - new Date().getTime())
    const [transitionVisible, setTransitionVisible] = useState(false)
    const [modalOpen, setModalOpen] = useState(false)
    const pathToRankImg = `${config.url}/ranks/${utils.returnImgSrcName(mmr)}.png`

    setTimeout(() => { setTransitionVisible(true) }, 300) // maybe bad / use useffect

    const prolongOrder = (hours) => {
        const token = localStorage.getItem('token')
        if (!token) return console.log('Cannot prolong order: No token')

        api.prolongOrder(token, orderId, hours, (err, response) => {
            if (err) {
                console.log('Cannot prolong: ', err)
                console.log('err.response.status', err.response.status)
                let alertMessage = ''
                if (err.response.status === 403)
                    alertMessage = 'Cannot prolong: Not enough balance'
                else if (err.response.status === 401)
                    alertMessage = 'Cannot prolong: Order not active'
                else if (err.response.status === 402)
                    alertMessage = 'Cannot prolong: Order is already finished'
                else if (err.response.status === 400)
                    alertMessage = 'Cannot prolong: No client found. Please login to account again or contact us.'
                else if (err.response.status === 404)
                    alertMessage = 'Cannot prolong: No Order found'

                console.log(alertMessage)
                alert(alertMessage)
                return
            } else {
                // Realod screen
                window.location.reload();
            }

        })
    }

    const isRealOrder = !!orderId

    const timerStyle = {
        position: 'relative',
        textAlign: 'center',
        paddingTop: '.3em',
        lineHeight: '1.3em',
        fontSize: '2em',
    }

    return (
        <Segment raised color={config.mainColor} compact
            style={{
                display: 'block',
                marginLeft: 'auto',
                marginRight: 'auto',
                width: isMobile ? '90vw' : '45%'
            }}>
            <Grid textAlign="center">
                <Transition visible={transitionVisible} animation='fade' duration="1400">
                    <div >
                        <Image size="small" src={pathToRankImg} />
                        <div style={{ fontSize: "180%", }}>{mmr} MMR</div>
                    </div>
                </Transition>
                <GridRow style={{ fontSize: '140%' }}>
                    <Grid.Column width={6}>
                        {t('personal-space.order.login')}
                    </Grid.Column>
                    <Grid.Column width={6}>
                        {t('personal-space.order.password')}
                    </Grid.Column>
                </GridRow>
                <GridRow>
                    <Grid.Column width={6}>
                        <Button onClick={() => navigator.clipboard.writeText(login)} content={t('personal-space.order.click-to-copy')} />
                    </Grid.Column>
                    <Grid.Column width={6}>
                        <Button onClick={() => navigator.clipboard.writeText(password)} content={t('personal-space.order.click-to-copy')} />
                    </Grid.Column>
                </GridRow>

                {
                    isRealOrder
                        ? <TimerWithProlongation
                            timerStyle={timerStyle}
                            t={t}
                            timeLeft={timeLeft}
                            setModalOpen={setModalOpen}
                            modalOpen={modalOpen}
                            prolongOrder={prolongOrder}
                            orderId={orderId}
                            accountId={accountId}
                        />
                        : <OnlyTimer
                            timerStyle={timerStyle}
                            t={t}
                            timeLeft={timeLeft}
                        />
                }
            </Grid>
        </Segment>
    )
}

const OnlyTimer = ({ timerStyle, t, timeLeft }) => (
    <div style={timerStyle}>
        <span>{t('personal-space.order.time-left')}</span>
        <br />
        <div>
            <Timer timeLeft={Math.trunc(timeLeft / 1000)} />
        </div>
    </div>
)

const TimerWithProlongation = (
    { timerStyle, t, timeLeft, setModalOpen, modalOpen, prolongOrder, orderId, accountId }
) => (
    <GridRow>
        <Grid.Column width={6}>
            <Statistic size='tiny'>
                <Statistic.Label>{t('personal-space.order.time-left')}</Statistic.Label>
                <Statistic.Value><Timer timeLeft={Math.trunc(timeLeft / 1000)} /></Statistic.Value>
            </Statistic>
        </Grid.Column>
        <Grid.Column width={6}>
            <Button
                positive
                content={t('personal-space.order.prolong.button')}
                onClick={() => setModalOpen(!modalOpen)}
            />
            {
                modalOpen
                    ? <ProlongModal
                        orderId={orderId}
                        accountId={accountId}
                        modalOpen={modalOpen}
                        setModalOpen={setModalOpen}
                        prolongOrder={prolongOrder}
                    />
                    : <></>
            }
        </Grid.Column>
    </GridRow>
)


export default Order