import React from "react"
import { Container } from "semantic-ui-react"

import '../accounts.css'

const PopupItems = ({ items }) => {
    const onlyUnique = (value, index, self) =>
        self.indexOf(value) === index;

    const calcImmortals = () => {
        let count = 0
        items.forEach(item => {
            if (item.rarity === 'Immortal')
                count += 1
        })
        return count
    }

    const calcArcanas = () => {
        let count = 0
        items.forEach(item => {
            if (item.rarity == 'Arcana' && item.type == 'hero-item')
                count += 1
        })
        return count
    }

    const getArrayOfHeroes = () => {
        const heroes = []
        items.forEach(item => {
            if (item.type == 'hero-item')
                heroes.push(item.hero)
        })
        const uniqueHeroes = heroes.filter(onlyUnique)
        return uniqueHeroes
    }

    const getHeroesString = () => {
        let list = ''
        getArrayOfHeroes().forEach(hero => {
            list += hero + ', '
        })
        list = list.slice(0, -2) // remove ,
        return list
    }

    const immortals = calcImmortals()
    const arcanas = calcArcanas()

    return (
        <div className='scrolling-items'>
            <Container content='Items' textAlign="center" />
            <div className="Immortal">Immortals: {immortals}</div>
            {
                arcanas !== 0 ?
                    <div className="Arcana">Arcanas: {arcanas}</div> : null
            }
            <div>Heroes: {getHeroesString()}</div>
        </div>
    )
}

export default PopupItems