import React from 'react';
import {
    Grid, Button, Image, Transition, Header, Container, GridColumn, Label
} from 'semantic-ui-react';
import { Link } from 'react-router-dom';
import config from '../../../config';
import { useTranslation } from 'react-i18next';


const SliderContent = ({
    textIsLeft, back, image, visible, text_header1,
    text_header2, fist_text, second_text, thrid_text,
    comingSoon
}) => {
    const { t } = useTranslation()

    const duration = '1250'
    const buttonDuration = '1750'
    const button_text = comingSoon ? 'Soon!' : t('home.begin-button-text')

    const styleWrapper = (background) => {
        return {
            backgroundImage: `url(${background})`,
            backgroundSize: 'cover',
            backgroundRepeat: 'no-repeat',
            height: '60vh',
        }
    }

    const styleDiv = {
        color: '#f5f5dc',
        position: 'relative',
        top: '35%',
        fontSize: '150%',
    }
    const buttonStyle = { marginTop: '30%', marginLeft: textIsLeft ? '90%' : '-90%' }

    const renderGeneralInfo = () => (
        <GridColumn width={8} textAlign={textIsLeft ? 'right' : 'left'}>
            <Transition
                animation={textIsLeft ? 'fade right' : 'fade left'}
                duration={duration}
                visible={visible}>
                <div style={styleDiv}>
                    <Header
                        as='h1'
                        style={{ color: '#f5f5dc', fontSize: '150%' }}
                        content={`${text_header1} ${text_header2}`}
                    />
                    <li style={{ marginTop: '2%' }}>{fist_text}</li>
                    <li style={{ marginTop: '2%' }}> {second_text}</li>
                    <li style={{ marginTop: '2%' }}>{thrid_text}</li>
                </div>
            </Transition>
            <Transition animation='zoom' duration={buttonDuration} visible={visible}>
                <Container textAlign='center'>
                    {
                        comingSoon
                            ? <Label
                                style={buttonStyle}
                                content={t('home.soon-label-text')}
                                color={config.mainColor}
                                size='huge'
                            />
                            : <Link to={config.pages.accounts}>
                                <Button
                                    style={buttonStyle}
                                    color={config.mainColor}
                                    size='large'
                                    content={button_text}
                                />
                            </Link>
                    }
                </Container>
            </Transition>
        </GridColumn>
    )

    const renderImage = () => (
        <GridColumn width={8}>
            <Image
                style={{ width: '400px' }}
                floated={textIsLeft ? 'left' : 'right'}
                size='large'
                src={image}
            />
        </GridColumn>
    )

    return (
        <div style={styleWrapper(back)}>
            <Grid>
                {
                    textIsLeft
                        ?
                        <> {renderGeneralInfo()} {renderImage()} </>
                        :
                        <> {renderImage()} {renderGeneralInfo()} </>
                }
            </Grid>
        </div>
    )
}

export default SliderContent;