import React, { useEffect, useState } from "react";
import { Grid, Button, Image, Transition, Header, Segment, GridColumn } from "semantic-ui-react";
import am from "../../../img/homePageImage/am.png"
import back from "../../../img/homePageImage/backBottom2.jpg"
import { Link } from "react-router-dom";
import config from "../../../config";
import BestAccountsGrid from './BestAccountsGrid'
import { useTranslation } from 'react-i18next';

//TODO: better if here be discount!
const BestAccounts = () => {
    const { t } = useTranslation()
    const [visible, setVisible] = useState(true)

    const styleWrapper = (background) => {
        return {
            backgroundImage: `url(${background})`,
            backgroundRepeat: "no-repeat",
            // backgroundSize: "cover", 
            height: "510px"
        }
    }
    useEffect(() => {
        const toggle = setInterval(() => {
            setVisible(prev => !prev);
        }, 4000)
        return () => {
            clearInterval(toggle)
        }
    },[])

    return (
        <div style={{ marginTop: '3vh', marginBottom: '0.1em', marginLeft: '0.1em', marginRight: '0.1em' }}>
            <Grid>
                <GridColumn width={4}>
                    <Segment raised color="red" style={styleWrapper(back)}>
                        <Header textAlign="center" as="h1" style={{ color: "#e6e6fa" }} content={t('home.best.header')} />
                        <Header textAlign="center" as='h3' style={{ color: "#e6e6fa" }} content={t('home.best.call-to-action1')} />
                        <Header textAlign="center" as='h3' style={{ color: "#e6e6fa" }} content={t('home.best.call-to-action2')} />
                        <Grid>
                            <GridColumn width={4}>
                                <Link to={config.pages.accounts}>
                                    <Button size='large' circular color={config.mainColor} content={t('home.best.button-to-accounts')}/>
                                </Link>
                            </GridColumn>
                            <GridColumn width={12}>
                                <Transition
                                    animation="pulse"
                                    duration='2000'
                                    visible={visible}
                                >
                                    <Image floated="right" size="medium" src={am} />
                                </Transition>
                            </GridColumn>
                        </Grid>

                    </Segment>
                </GridColumn>
                <GridColumn width={12}>
                    <Segment raised color="red" style={styleWrapper(back)} >
                        <BestAccountsGrid />
                    </Segment>
                </GridColumn>
            </Grid>
        </div >


    )
}

export default BestAccounts;