import React, { useEffect } from 'react';
import { Header, Divider, Segment } from "semantic-ui-react";
import text from './termsText'
import './clauses.css'
import { isMobile } from "react-device-detect";
import config from "../../config";
import ReactGA from 'react-ga'


const TermsOfUse = () => {
    useEffect(() => {
        ReactGA.pageview(window.location.hash.slice(1))
    }, [])

    const returnClauses = (arrayOfClauses) => {
        const array = []

        arrayOfClauses.forEach(clause => {
            array.push(
                <div className="clause">
                    {clause}
                </div>
            )
        });
        return array
    }

    const segmentStyle = {
        marginLeft: 'auto',
        marginRight: 'auto',
        width: isMobile ? '97%' : '60%',
        // display: 'flex',
        // justifyContent: 'center',
        // width: '100%'
    }

    const returnHeadlines = () => {
        const array = []
        for (let headline in text) {
            array.push(
                <div>
                    <Header content={headline} />
                    {returnClauses(text[headline])}
                    < Divider />
                </div>
            )
        }
        return array
    }

    return (
        <div style={{ margin: '1em', marginBottom: '2%' }}>
            <p />
            <Header content='Terms of use' textAlign="center" />
            <p />
            <Segment
                color={config.mainColor}
                raised
                style={segmentStyle}
                textAlign="center">
                {returnHeadlines()}
            </Segment>
        </div>
    )
}

export default TermsOfUse