import React from "react"
import {
    Header,
    Button,
} from "semantic-ui-react"


class TagList extends React.Component {
    intervalId = 0
    constructor(props) {
        super(props);
        this.state = {
            dotaplus: false,
            vip: false,
            lowmmr: false,
            wtfPrice: false,
            coolitems: false,
            highmmr: false,
            battlepass: false,
        }
    }
    componentDidMount() {
        this.props.setTags(this.state)

        // Flicker all tags once
        if (this.allTagsAreDisabled())
            this.flickerAllTags()
    }

    flickerAllTags() {
        const startTime = 700
        const stepTime = 150

        setTimeout(() => {
            this.disableAllTags(); this.setState({ dotaplus: true })
        }, startTime + stepTime * 0)
        setTimeout(() => {
            this.disableAllTags(); this.setState({ vip: true })
        }, startTime + stepTime * 1)
        setTimeout(() => {
            this.disableAllTags(); this.setState({ lowmmr: true })
        }, startTime + stepTime * 2)
        setTimeout(() => {
            this.disableAllTags(); this.setState({ wtfPrice: true })
        }, startTime + stepTime * 3)
        setTimeout(() => {
            this.disableAllTags(); this.setState({ coolitems: true })
        }, startTime + stepTime * 4)
        setTimeout(() => {
            this.disableAllTags(); this.setState({ highmmr: true })
        }, startTime + stepTime * 5)
        setTimeout(() => {
            this.disableAllTags(); this.setState({ battlepass: true })
        }, startTime + stepTime * 6)
        setTimeout(() => {
            this.disableAllTags()
        }, startTime + stepTime * 7)
    }

    allTagsAreDisabled() {
        return !this.state.dotaplus
            && !this.state.vip
            && !this.state.lowmmr
            && !this.state.wtfPrice
            && !this.state.coolitems
            && !this.state.highmmr
            && !this.state.battlepass
    }

    disableAllTags() {
        this.setState({
            dotaplus: false,
            vip: false,
            lowmmr: false,
            wtfPrice: false,
            coolitems: false,
            highmmr: false,
            battlepass: false
        })
    }

    //toggle functions is async cause setState is promise and if u try to
    //do it without await it will setTags for last render, and change state after state changed
    async toggleDotaplus() {
        await this.setState({ dotaplus: !this.state.dotaplus })
        await this.props.setTags(this.state)
    }
    async toggleVip() {
        await this.setState({ vip: !this.state.vip })
        await this.props.setTags(this.state)
    }
    async toggleLowmmr() {
        await this.setState({ lowmmr: !this.state.lowmmr })
        await this.props.setTags(this.state)
    }
    async toggleWtfprice() {
        await this.setState({ wtfPrice: !this.state.wtfPrice })
        await this.props.setTags(this.state)
    }
    async toggleCoolitems() {
        await this.setState({ coolitems: !this.state.coolitems })
        await this.props.setTags(this.state)
    }
    async toggleHighmmr() {
        await this.setState({ highmmr: !this.state.highmmr })
        await this.props.setTags(this.state)
    }
    async toggleBattlepass() {
        await this.setState({ battlepass: !this.state.battlepass })
        await this.props.setTags(this.state)
    }

    createButton(color, content, stateControll) {
        let onClick
        switch (content) {
            case 'Dota+':
                onClick = () => this.toggleDotaplus(); break
            case 'Vip':
                onClick = () => this.toggleVip(); break
            case 'Low MMR':
                onClick = () => this.toggleLowmmr(); break
            case 'WTF Price':
                onClick = () => this.toggleWtfprice(); break
            case 'Cool Items':
                onClick = () => this.toggleCoolitems(); break
            case 'High MMR':
                onClick = () => this.toggleHighmmr(); break
            case 'Battlepass':
                onClick = () => this.toggleBattlepass(); break
            default: break
        }
        return (
            <Button color={stateControll ? color : 'grey'} content={content}
                style={{
                    opacity: stateControll ? '1' : '.5',
                    ...this.buttonStyle
                }}
                size="small"
                id={content}
                onClick={onClick}
            />
        )
    }

    buttonStyle = { margin: '2.5%' }

    render() {
        return (
            <div>
                <Header content={this.props.t('accounts.find-by-tags')} />
                {this.createButton('yellow', 'Dota+', this.state.dotaplus)}
                {this.createButton('purple', 'Vip', this.state.vip)}
                {this.createButton('brown', 'Low MMR', this.state.lowmmr)}
                {this.createButton('red', 'WTF Price', this.state.wtfPrice)}
                {this.createButton('pink', 'Cool Items', this.state.coolitems)}
                {this.createButton('blue', 'High MMR', this.state.highmmr)}
                {this.createButton('teal', 'Battlepass', this.state.battlepass)}
            </div>
        )
    }
}

export default TagList