import React, { useEffect, useState } from 'react';
import { Link, useHistory, useLocation } from "react-router-dom"
import { Button, Icon, Dropdown } from 'semantic-ui-react'
import config from "../../config";
import MobileHeader from './MobileHeader'
import { useTranslation } from "react-i18next";


const SwitchButtons = () => {
    const { t } = useTranslation()
    let history = useHistory();
    const location = useLocation();

    const [selectedRightButton, setSelectedRightButton] = useState('Personal space')

    useEffect(() => {
        // Define selected top button
        if (location.pathname.includes('balance')) {
            setSelectedRightButton(t('header.menu.balance'))
        }
        else {
            setSelectedRightButton(t('header.personal-space'))
        } 
    }, [location])

    const switchPages = (value) => {
        switch (value) {
            case 'balance':
                history.push(config.pages.balance)
                break;

            case 'logout':
                localStorage.removeItem("token");
                window.location.reload();
                break;

            default: //'personalSpace'
                history.push(config.pages.personalSpace)
                break;
        }
    }

    const renderButton = (icon, link, nameBtn, addMarginLeft, disabled = false) => {
        const someStyle = addMarginLeft ? { marginLeft: '20px', marginTop: '16px' } : { marginTop: '16px' }
        return (
            !disabled ?
                <Link to={link}>
                    <div style={someStyle}>
                        <Button color={config.headerButtonColor}>
                            <Icon name={icon}></Icon>
                            {nameBtn}
                        </Button>
                    </div>
                </Link>
                :
                <div style={someStyle}>
                    <Button inverted color={config.headerButtonColor} disabled>
                        <Icon name={icon}></Icon>
                        {nameBtn}
                    </Button>
                </div>
        )
    }

    const options = [
        { key: 'personalSpace', icon: 'user outline', text: t('header.personal-space'), value: 'personalSpace' },
        { key: 'balance', icon: 'money bill outline', text: t('header.menu.balance'), value: 'balance' },
        { key: 'logout', icon: 'log out', text: 'Logout', value: 'logout' },
    ]

    // Balance translation is not the same with mobile version
    return (
        <div style={{ marginLeft: '3em' }}>
            <Button.Group>
                {renderButton("home", config.pages.home, t('header.home'), false, false)}
                {renderButton("shopping cart", config.pages.accounts, t('header.accounts'), true, false)}
                {renderButton("crosshairs", config.pages.streamSnipe, t('header.stream-snipe'), true, true)}
                {renderButton("trophy", config.pages.battleCup, t('header.battle-cup'), true, true)}
            </Button.Group>

            <Button.Group
                floated="right"
                color={config.headerButtonColor}
                style={{ marginTop: '16px', marginRight: '20px' }}>
                <Dropdown
                    className='button'
                    options={options}
                    onChange={(e, { value }) => { switchPages(value) }}
                    placeholder={selectedRightButton}
                />
            </Button.Group>
        </div >
    )
}

const Header = ({ isMobile }) => {
    return isMobile ?
        <MobileHeader />
        :
        <div className='top-bar'>
            <SwitchButtons />
        </div>
}

export default Header