import React from "react";
import { Divider, Input, Button } from "semantic-ui-react";
import { Checkmark } from 'react-checkmark'

const CPRender = ({ newPassword, setNewPassword, confirmNewPassword, t, changePassword,
    setConfirmNewPassword, error, setError, errorText, isLoading, isCheckmarkHidden }) => {
    return (
        <div style={{ minHeight: '95vh', textAlign: 'center' }}>
            <Divider hidden />
            {error ?
                <>
                    <Input placeholder={t('change-password.new-password')} value={newPassword}
                        onChange={(e) => {
                            setNewPassword(e.target.value)
                            setError(false)
                        }} error />
                    <Divider hidden />
                    <Input placeholder={t('change-password.confirm')} value={confirmNewPassword}
                        onChange={(e) => {
                            setConfirmNewPassword(e.target.value)
                            setError(false)
                        }} error />
                    <div style={{ color: '#ed0a34', marginTop: '5px' }}>
                        {errorText}
                    </div>
                    <Divider hidden />
                </>
                :
                <>
                    <Input placeholder={t('change-password.new-password')} value={newPassword}
                        onChange={(e) => setNewPassword(e.target.value)} />
                    <Divider hidden />
                    <Input placeholder={t('change-password.confirm')} value={confirmNewPassword}
                        onChange={(e) => setConfirmNewPassword(e.target.value)} />
                    <Divider hidden />
                </>
            }
            {isLoading ?
                <Button content={t('change-password.change')} loading onClick={changePassword} positive />
                :
                <>
                    {isCheckmarkHidden ?
                        <Button content={t('change-password.change')} onClick={changePassword} positive />
                        : <Checkmark />}
                </>
            }
        </div>
    )
}

export default CPRender