import React, { useEffect } from "react";
import HomeSlider from "./desktop/HomeSlider";
import BestAccounts from "./desktop/BestAccounts";
import MobileHomeSlider from "./mobile";
import MobileBestAccounts from "./mobile/BestAccounts";
import { isMobile } from "react-device-detect";
import ReactGA from 'react-ga'


const Home = () => {
    useEffect(() => {
        ReactGA.pageview(window.location.hash.slice(1))
    }, [])

    return isMobile ?
        <div style={{ minHeight: '125vh' }}>
            <MobileHomeSlider />
            <MobileBestAccounts />
        </div>
        :
        <div style={{ minHeight: '125vh' }}>
            <HomeSlider />
            <BestAccounts />
        </div >
}

export default Home