import React, { useState, useEffect } from 'react'
import { isMobile } from 'react-device-detect'
import config from '../../../config'
import * as utils from '../../../utils'
import AccountCard from './AccountCard.jsx'


const MainAccountCard = (
    { rating, behavior, trophyLevel, costPerHour, _id = "61cb8fb55b53955193bfc30e",
        items = [], accountTagsAvailable,
        prices, busyAccount,
        tags = [
            // Possible tags:
            // { color: 'yellow', name: 'Dota+' },
            // { color: 'purple', name: 'Vip' },
            // { color: 'brown', name: 'Low MMR' },
            // { color: 'red', name: 'WTF Price' },
            // { color: 'pink', name: 'Cool Items' },
            // { color: 'blue', name: 'High MMR' },
            // { color: 'teal', name: 'Battlepass' },
        ] }
) => {
    const [tagsObjects, setTagsObjects] = useState()
    const [payForRentOpen, setPayForRentOpen] = useState(false)
    const [itemsOpen, setItemsOpen] = useState(false)
    const backend_url = config.url
    const pathToAccountImage = `${backend_url}/account-card/${_id}.jpg`
    const haveItems = items.length === 0 ? false : true

    useEffect(() => {
        setTagsObjects(utils.accountTagsFromString(tags))
    }, [tags])


    return (
        <AccountCard
            itemsOpen={itemsOpen}
            setItemsOpen={setItemsOpen}
            rating={rating}
            tags={tags}
            items={items}
            behavior={behavior}
            costPerHour={costPerHour}
            tagsObjects={tagsObjects}
            pathToAccountImage={pathToAccountImage}
            trophyLevel={trophyLevel}
            _id={_id}
            haveItems={haveItems}
            setPayForRentOpen={setPayForRentOpen}
            payForRentOpen={payForRentOpen}
            accountTagsAvailable={accountTagsAvailable}
            prices={prices}
            busyAccount={busyAccount}
        />
    )
}

export default MainAccountCard