import React, { useState, useEffect } from "react";
import { Input, Button, Transition, Form, Checkbox } from "semantic-ui-react";
import { useTranslation } from "react-i18next";
import { Link } from 'react-router-dom'
import config from "../../config";


const DefaultCase = ({
    email, setEmail, password, setPassword, handleLogin, handleRegister,
    toggleVisible, loadingRegister, loadingLogin, isError, errorMsg,
    setIsError, setAgreeTerms, agreeTerms
}) => {
    const { t } = useTranslation()
    return (
        <div id='login-register-form'>
            <Input placeholder='Email' icon='mail outline' error={isError}
                value={email} onChange={(e) => {
                    setEmail(e.target.value)
                    setIsError(false)
                }} /> <p />
            <Input
                placeholder='Password'
                icon='key'
                error={isError}
                type='password'
                value={password} onChange={(e) => {
                    setPassword(e.target.value)
                    setIsError(false)
                }} />
            <p />
            <div>
                <Checkbox
                    checked={agreeTerms}
                    onChange={(e, w) => {
                        setAgreeTerms(w.checked)
                    }}
                    label={
                        <label>
                            {t('personal-space.terms-req')}
                            <Link to={config.pages.termsOfUse}>
                                {t('personal-space.terms-req-2')}
                            </Link>
                        </label>
                    }
                />
            </div>

            {isError ?
                <div style={{ color: '#ed0a34', marginTop: '5px' }}>
                    {errorMsg}
                </div> : null}
            <p />
            <Button.Group size='large'>
                {loadingLogin ?
                    <Button loading content='Login' />
                    :
                    <Button onClick={handleLogin} content='Login' />
                }
                <Button.Or />
                {loadingRegister ?
                    <Button loading content='Register' />
                    :
                    <Button onClick={handleRegister} content='Register' />
                }
            </Button.Group>
            <p />
            <Button content='Reset password' onClick={toggleVisible} />
        </div>
    )
}

const ResetCase = ({
    email, setEmail, resetPassword, isError, errorMsg
}) => {
    const [visibleOld, setVisibleOld] = useState(true)
    const [visibleNew, setVisibleNew] = useState(false)
    useEffect(() => {
        setVisibleOld(false)
        setTimeout(() => { setVisibleNew(true) }, 1100)
    }, [])

    return (
        <div id='login-register-form'>
            {/* I create Form here cause many browsers 
            using this to autocomplete data on next login */}
            <Form>
                <Form.Input placeholder='Email' icon='mail outline' error={isError}
                    value={email} onChange={(e) => setEmail(e.target.value)} />
                {isError ?
                    <div style={{ color: '#ed0a34', marginTop: '5px' }}>
                        {errorMsg}
                    </div> : null}
                <p />
                <Transition visible={visibleNew} animation='fade' duration={1000}>
                    <div style={{ textAlign: 'center' }} >
                        <Button content='Reset password' onClick={resetPassword} />
                    </div>
                </Transition>

                {/* Will be hidden */}
                <Transition visible={visibleOld} animation='fade' duration={1000}>
                    <div>
                        <Form.Input placeholder='Password' icon='key' type='password' />
                        <p />
                        <Button.Group size='large' >
                            <Button type='submit'>
                                Login
                            </Button>
                            <Button.Or />
                            <Button >
                                Register
                            </Button>
                        </Button.Group>
                        <p />
                        <Button content='Reset password' />
                    </div>
                </Transition>
            </Form>
        </div>
    )
}

const LRFRender = ({
    email, setEmail, password, setPassword, handleLogin, handleRegister, isError,
    isResetPWClicked, resetPassword, toggleVisible, loadingRegister, loadingLogin,
    errorMsg, setIsError, setAgreeTerms, agreeTerms
}) => {

    if (isResetPWClicked)
        return (
            <ResetCase email={email} setEmail={setEmail} resetPassword={resetPassword}
                isError={isError} errorMsg={errorMsg} />
        )
    else
        return (
            <DefaultCase email={email}
                setEmail={setEmail}
                password={password}
                setPassword={setPassword}
                handleLogin={handleLogin}
                handleRegister={handleRegister}
                toggleVisible={toggleVisible}
                loadingRegister={loadingRegister}
                loadingLogin={loadingLogin}
                isError={isError}
                errorMsg={errorMsg}
                setIsError={setIsError}
                setAgreeTerms={setAgreeTerms}
                agreeTerms={agreeTerms} />
        )
}

export default LRFRender