import React from "react";
import { useTranslation } from "react-i18next";
import {
    Icon,
    Table
} from "semantic-ui-react";
import { replenishmentType } from './_types'


const ReplenishmentHistory = (props: { replenishments: replenishmentType[] }): JSX.Element => {
    const replenishments: replenishmentType[] = props.replenishments
    const { t } = useTranslation()

    const renderReplenishments = () => replenishments.map(
        (repl: replenishmentType) => <Replenishment
            amount={repl.amount}
            updatedAt={repl.updatedAt}
            finished={repl.finished}
            successful={repl.successful}
            key={repl._id}
            _id={repl._id}
        />
    )

    return (
        <Table striped textAlign="center">
            <Table.Header>
                <Table.Row>
                    <Table.HeaderCell>
                        {t('balance.replenishment.date')}
                    </Table.HeaderCell>
                    <Table.HeaderCell>
                        {t('balance.replenishment.amount')}
                    </Table.HeaderCell>
                    <Table.HeaderCell>
                        {t('balance.replenishment.successful')}
                    </Table.HeaderCell>
                </Table.Row>
            </Table.Header>
            <Table.Body>
                {renderReplenishments()}
            </Table.Body>
        </Table>
    )
}

const Replenishment = (repl: replenishmentType) => {
    const { updatedAt, amount, successful, finished } = repl
    const month = new Date(updatedAt).getMonth() < 10 ?
        '0' + new Date(updatedAt).getMonth() : String(new Date(updatedAt).getMonth())

    const dateStringForUser =
        new Date(updatedAt).getDate()
        + '.' + (parseInt(month) + 1)
        + '.' + new Date(updatedAt).getFullYear()

    return (
        <Table.Row>
            <Table.Cell>{dateStringForUser}</Table.Cell>
            <Table.Cell>{amount} $</Table.Cell>
            <Table.Cell>
                {successful && finished
                    ?
                    <Icon name="check" color="green" />
                    :
                    <div style={{ color: 'red' }}><Icon name="close" /></div>
                    //if u give color red as icon props it ll break icon
                }
            </Table.Cell>
        </Table.Row>
    )
}

export default ReplenishmentHistory