import React from 'react'
import {
    Grid,
    Image,
    Divider,
    Transition
} from 'semantic-ui-react'
import voidSpirit from '../../../img/homePageImage/void_spirit.png'
import TagList from './TagList'
import { useTranslation } from 'react-i18next'
import config from '../../../config'
import Slider, { Range } from 'rc-slider';
import 'rc-slider/assets/index.css';


const DesktopSearch = ({
    setMinMmr, setMaxMmr, priceToRender,
    maxPriceUSD, setMaxPrice, setTags,
    visible
}) => {
    const { t } = useTranslation()

    return (
        <Grid verticalAlign='middle'>
            <Grid.Row columns={3}>
                <Grid.Column>
                    <Transition visible={visible} duration={1000} animation='fade right'>
                        <Image size='medium' src={voidSpirit} />
                    </Transition>
                </Grid.Column>
                <Grid.Column textAlign="center">
                    {/* TagList gets t cause its class */}
                    <TagList setTags={setTags} t={t} />
                </Grid.Column>
                <Grid.Column>
                    <div style={{ textAlign: "center", marginTop: '2em', marginRight: '2em' }}>
                        <div>{t('accounts.search.wished-mmr')}</div>
                        <Range
                            min={config.availableMMRs.min}
                            max={config.availableMMRs.max}
                            defaultValue={[config.availableMMRs.min, config.availableMMRs.max]}
                            marks={{ '10': 10, '2000': 2000, '4000': 4000, '6000': 6000 }}
                            pushable={500}
                            onChange={(changedValue) => {
                                const min = changedValue[0]
                                const max = changedValue[1]
                                setMinMmr(min)
                                setMaxMmr(max)
                            }}
                            trackStyle={[{ backgroundColor: config.colors.trackColor }]}
                        />
                        <Divider hidden />

                        <div>{t('accounts.search.max-price')}: {priceToRender() + ' ' + t('currency')}</div>
                        <Slider
                            min={config.prices.accountsMin}
                            max={config.prices.accountsMax}
                            step={0.1}
                            placeholder={t('accounts.search.max-price')}
                            value={maxPriceUSD}
                            onChange={(number) => setMaxPrice(number)}
                            trackStyle={{ backgroundColor: config.colors.trackColor }}
                        />
                    </div>
                </Grid.Column>
            </Grid.Row>
        </Grid>
    )
}

export default DesktopSearch
