import React, { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import ReactGA from 'react-ga'
import * as api from '../../api/index'
import Order from "./Order";
import { setRole } from "../../redux/roleSlice";
import { setOrders } from '../../redux/ordersSlice'

import PSRender from "./PSRender";

const PersonalSpace = () => {
    const role = useSelector(state => state.role.role)
    const orders = useSelector(state => state.orders.orders)
    const dispatch = useDispatch()
    //orders is array of objects with props: mmr, deadline(in iso format) login, password

    useEffect(() => {
        ReactGA.pageview(window.location.hash.slice(1))

        const token = localStorage.getItem('token')
        if (token)
            api.getClientOrders(token, (err, res) => {
                console.log('getClientOrders res:', res);
                if (err && err.response && err.response.data && err.response.data === 'Not Acceptable') {
                    dispatch(setRole('auth-not-confirmed'))
                    return true
                }
                if (!res || !res.data) return

                switch (JSON.stringify(res.data)) {
                    case '[]':
                        dispatch(setRole('auth-not-made-order'))
                        break
                    default:
                        dispatch(setRole('auth-and-made-order'))
                        dispatch(setOrders(res.data))
                        break
                }
            })
        else dispatch(setRole('unauth'))
    }, [])

    const logout = () => {
        localStorage.removeItem("token");
        window.location.reload();
    }

    const returnClientOrders = () => {
        console.log('returnClientOrders', orders)
        return orders.map((order) =>
            <Order
                key={order.orderId}
                orderId={order.orderId}
                accountId={order.accountId}
                mmr={order.mmr}
                deadline={new Date(order.deadline)}
                login={order.login}
                password={order.password}
            />
        )
    }

    const resendEmail = (setMessage, t) => {
        const token = localStorage.getItem('token')
        api.resendEmail(token, (err, res) => {
            if (err?.response?.data === "Forbidden")
                return setMessage(t('personal-space.email-already-resend'))
            if (res?.data === 'not time yet')
                return setMessage(t('personal-space.email-resend-not-time-yet'))
            if (res?.data === 'OK')
                return setMessage(t('personal-space.resended-success'))
            // if (err) setMessage(err.response.data)
            // if (res) setMessage(res.data)
        })
    }

    return <PSRender
        role={role}
        returnClientOrders={returnClientOrders}
        logout={logout}
        resendEmail={resendEmail}
    />
}

export default PersonalSpace