import React, { useState, useEffect } from "react";

import { Header, Loader } from "semantic-ui-react";
import * as api from '../../api/index'
import config from '../../config'


const SuccessfulPayment = () => {
    const [responseCode, setResponseCode] = useState(0)
    let code = window.location.href.split('=')[1];

    useEffect(() => {
        console.log('successful code:', code)
        api.successfulPayment(code, localStorage.getItem('token'), (err, resp) => {
            console.log('successfulPayment err', err);
            console.log('successfulPayment resp', resp);
            setResponseCode(resp)

            if (resp === 200)
                window.open(config.frontend_url + config.pages.balance, "_self")
        })
    }, [])

    switch (responseCode) {
        case 400:
            return (
                <div style={{ minHeight: '95vh', textAlign: 'center' }}>
                    <Header style={{ marginTop: '3em' }} content='Href available no more' />
                </div>
            )
        case 200:
            return (
                <div style={{ minHeight: '95vh', textAlign: 'center' }}>
                    <Header style={{ marginTop: '3em' }} content='Successful' />
                </div>
            )
        default:
            return (
                <div style={{ minHeight: '95vh', textAlign: 'center' }}>
                    <Loader style={{ marginTop: '3em' }} active inline >
                        loading
                    </Loader>
                </div>
            )
    }
}

export default SuccessfulPayment