import React, { useEffect, useState } from "react";
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import SliderContent from "./SliderContent";

import { useTranslation } from 'react-i18next';
import back from "../../../img/homePageImage/back.jpg"
import back2 from "../../../img/homePageImage/back2.jpg"
import back3 from "../../../img/homePageImage/back3.jpg"
import back4 from "../../../img/homePageImage/back4.jpg"
import back5 from "../../../img/homePageImage/back5.jpg"
import voker from "../../../img/homePageImage/6.png"
import qop from "../../../img/homePageImage/11.png"
import slark from "../../../img/homePageImage/4.png"
import faceless from "../../../img/homePageImage/5.png"
import luna from "../../../img/homePageImage/7.png"

//TODO: maybe need do pool image to array, for change image when be next slide


const HomeSlider = () => {
    const { t } = useTranslation()
    const duration = "1250";
    const [visible1, setVisible1] = useState(false);
    const [visible2, setVisible2] = useState(false);
    const [visible3, setVisible3] = useState(false);
    const [visible4, setVisible4] = useState(false);
    const [visible5, setVisible5] = useState(false);
    const [activeSlide, setActiveSlide] = useState(0);

    const settings = {
        dots: true,
        infinite: true,
        speed: 800,
        slidesToShow: 1,
        slidesToScroll: 1,
        autoplay: true,
        autoplaySpeed: 5500,
        beforeChange: (current, next) => setActiveSlide(next),
    }

    useEffect(() => {
        //maybe better switch-case
        if (activeSlide === 0) {
            setVisible1(true);
            setVisible2(false);
            setVisible3(false);
            setVisible4(false);
            setVisible5(false);
        }
        if (activeSlide === 1) {
            setVisible1(false);
            setVisible2(true);
            setVisible3(false);
            setVisible4(false);
            setVisible5(false);
        }
        if (activeSlide === 2) {
            setVisible1(false);
            setVisible2(false);
            setVisible3(true);
            setVisible4(false);
            setVisible5(false);
        }
        if (activeSlide === 3) {
            setVisible1(false);
            setVisible2(false);
            setVisible3(false);
            setVisible4(true);
            setVisible5(false);
        }
        if (activeSlide === 4) {
            setVisible1(false);
            setVisible2(false);
            setVisible3(false);
            setVisible4(false);
            setVisible5(true);
        }
    }, [activeSlide]);

    return (

        <div>
            <Slider {...settings}>
                <div>
                    <SliderContent textIsLeft={false}
                        back={back}
                        image={voker}
                        visible={visible1}
                        text_header1={t('home.why')}
                        text_header2={t('home.rent-account-text-left')}
                        fist_text={t('home.descriptions.left-first')}
                        second_text={t('home.descriptions.left-second')}
                        thrid_text={t('home.descriptions.left-third')}
                        comingSoon={false}
                    />
                </div>
                <div>
                    <SliderContent textIsLeft={true}
                        back={back2}
                        image={faceless}
                        visible={visible2}
                        text_header1={t('home.how')}
                        text_header2={t('home.rent-account-text-right')}
                        fist_text={t('home.descriptions.right-first')}
                        second_text={t('home.descriptions.right-second')}
                        thrid_text={t('home.descriptions.right-third')}
                        comingSoon={false}
                    />
                </div>
                <div>
                    <SliderContent textIsLeft={false}
                        back={back4}
                        image={luna}
                        visible={visible3}
                        text_header1={t('home.battle-pass.header1')}
                        text_header2={t('home.battle-pass.header2')}
                        fist_text={t('home.battle-pass.text1')}
                        second_text={t('home.battle-pass.text2')}
                        thrid_text={t('home.battle-pass.text3')}
                        comingSoon={true}
                    />
                </div>
                <div>
                    <SliderContent textIsLeft={true}
                        back={back3}
                        image={slark}
                        visible={visible4}
                        text_header1={t('home.battle-cup.header1')}
                        text_header2={t('home.battle-cup.header2')}
                        fist_text={t('home.battle-cup.text1')}
                        second_text={t('home.battle-cup.text2')}
                        thrid_text={t('home.battle-cup.text3')}
                        comingSoon={true}
                    />
                </div>
                <div>
                    <SliderContent textIsLeft={false}
                        back={back5}
                        image={qop}
                        visible={visible5}
                        text_header1={t('home.stream-snipe.header1')}
                        text_header2={t('home.stream-snipe.header2')}
                        fist_text={t('home.stream-snipe.text1')}
                        second_text={t('home.stream-snipe.text2')}
                        thrid_text={t('home.stream-snipe.text3')}
                        comingSoon={true}
                    />
                </div>
            </Slider >
        </div >
    );
}
export default HomeSlider;