export const resendEmail = (token: token, callback: any) => {
    axios({
        method: "get",
        url: url + config.api.resendEmail,
        headers: { 'Authorization': `Bearer ${token}` }
    }).then((res) => {
        callback(false, res)
    }).catch((err) => {
        callback(err, false)
    })
}

