export const makeOrder = (accountId: string, hours: string | number,
    region: string, token: token, promo: string, callback: any) => {
    if (accountId.length !== 24) throw new Error('invalid id length:' + accountId)
    axios({
        method: 'POST',
        url: url + config.api.makeOrder,
        data: {
            items: [
                { id: accountId }
            ],
            accountId, hours, region, promo
        },
        headers: { 'Authorization': 'Bearer ' + token }
    }).then(res => {
        callback(false, 'Successful')
    }).catch(err => {
        callback(err, false)
    })
}

