export const successfulPayment = (code: string, token: token, callback: any) => {
    const resultUrl = url + config.api.successfulPayment + `?code=${code}`
    console.log('resultUrl', resultUrl)
    axios({
        method: 'GET',
        url: resultUrl,
        headers: { 'Authorization': 'Bearer ' + token }
    }).then(res => {
        console.log('successfulPayment res', res)
        callback(false, 200)
    }).catch(err => {
        console.log('successfulPayment err:', err)
        if (err.response) {
            callback(err, err.response.status)
        } else {
            callback(err, 499)
        }
    })
}

