import React from 'react'
import { useTranslation } from 'react-i18next'
import TagList from './TagList'

const MobileSearch = ({ setTags }) => {
    // t is created here cause TagList is a Class Component :D
    
    //TagList is a class component cause you cant ezly toggle state
    //in funtional component
    //But its fun, yeah
    const { t } = useTranslation() 
    return (
        <div>
            <TagList setTags={setTags} t={t} />
        </div>
    )
}

export default MobileSearch
