import React, { useState, useEffect } from 'react'
import { Grid, Header, Icon, Segment } from 'semantic-ui-react'
import * as api from '../../api'
import config from '../../config'
import bountyRune from '../../img/bounty-rune.png'
import AnimatedImage from '../../сomponents/AnimatedImage'
import { isMobile } from 'react-device-detect'
import { useTranslation } from 'react-i18next'


const ProgressSteps = () => {
    const [registered, setRegistered] = useState(false)
    const [activated, setActivated] = useState(false)
    const [toppedUp, setToppedUp] = useState(false)
    const [rented, setRented] = useState(false)

    const [bountyAnimation, setBountyAnimation] = useState(true)

    const { t } = useTranslation()

    const animationDelay = 20
    const bountyAnimationDuration = 1200

    useEffect(() => {
        const token = localStorage.getItem('token')
        if (!token) return

        // Registered
        api.getEmail(token, (err, result) => setRegistered(!err))

        // activated
        api.emailConfirmed(token, (err, result) => setActivated(result.data !== 'email not confirmed'))

        // toppedUp
        api.getBalance(token, (err, result) => setToppedUp(result !== 0))

        // rented
        api.getOrdersHistory(token, (err, result) => setRented(result.length !== 0))


        //  Bounty Runes
        // Animate Bounty
        const animationId = setInterval(() => {
            setBountyAnimation(prev => !prev)
        }, bountyAnimationDuration * 2 + animationDelay)

        return () => {
            clearInterval(animationId)
        }
    }, [])

    const mobileStyle = {
        marginTop: '4em',
        marginBottom: '4em',
        width: '95%',
        marginLeft: 'auto',
        marginRight: 'auto'
    }

    const desktopStyle = {
        marginTop: '4em',
        marginBottom: '4em',
        width: '80%',
        marginLeft: 'auto',
        marginRight: 'auto'
    }

    const step1 = () =>
        <Step
            done={registered}
            header={t('balance.progress.acc-registered.header')}
            subheader1={t('balance.progress.acc-registered.subheader1')}
            subheader2={t('balance.progress.acc-registered.subheader2')}
            visibleAnimation={bountyAnimation}
            animationDuration={bountyAnimationDuration}
        />

    const step2 = () =>
        <Step
            done={activated}
            header={t('balance.progress.acc-activated.header')}
            subheader1={t('balance.progress.acc-activated.subheader1')}
            subheader2={t('balance.progress.acc-activated.subheader2')}
            visibleAnimation={bountyAnimation}
            animationDuration={bountyAnimationDuration}
        />

    const step3 = () =>
        <Step
            done={toppedUp}
            header={t('balance.progress.balance.header')}
            subheader1={t('balance.progress.balance.subheader1')} //
            subheader2={t('balance.progress.balance.subheader2')}
            visibleAnimation={bountyAnimation}
            animationDuration={bountyAnimationDuration}
        />

    const step4 = () =>
        <Step
            done={rented}
            header={t('balance.progress.rented.header')}
            subheader1={t('balance.progress.rented.subheader1')}
            subheader2={t('balance.progress.rented.subheader2')}
            visibleAnimation={bountyAnimation}
            animationDuration={bountyAnimationDuration}
        />

    const MobileGrid = () => (
        <Grid centered verticalAlign='middle'>
            <Grid.Row>
                <Grid.Column textAlign='center' key='step1'>
                    {step1()}
                </Grid.Column>
            </Grid.Row>
            <Grid.Row>

                <Grid.Column textAlign='center' key='step2'>
                    {step2()}
                </Grid.Column>
            </Grid.Row>
            <Grid.Row>
                <Grid.Column textAlign='center' key='step3'>
                    {step3()}
                </Grid.Column>
            </Grid.Row>
            <Grid.Row>

                <Grid.Column textAlign='center' key='step4'>
                    {step4()}
                </Grid.Column>
            </Grid.Row>
        </Grid>
    )

    const DesktopGrid = () => (
        <Grid columns={2} centered verticalAlign='middle'>
            <Grid.Row>
                <Grid.Column width={6} textAlign='center'>
                    {step1()}
                </Grid.Column>
                <Grid.Column width={6} textAlign='center'>
                    {step2()}
                </Grid.Column>
            </Grid.Row>
            <Grid.Row>
                <Grid.Column width={6} textAlign='center'>
                    {step3()}
                </Grid.Column>
                <Grid.Column width={6} textAlign='center'>
                    {step4()}
                </Grid.Column>
            </Grid.Row>
        </Grid>
    )

    return (
        <Segment
            color={config.mainColor}
            raised
            style={isMobile ? mobileStyle : desktopStyle}>
            {/* <Header color={config.mainColor} as='h1'>{t('balance.progress.header')}</Header> */}
            {
                isMobile
                    ? <MobileGrid />
                    : <DesktopGrid />
            }
        </Segment>
    )
}

const Step = ({ done, header, subheader1, subheader2, visibleAnimation, animationDuration }) => (
    <>
        {done
            ? <AnimatedImage
                src={bountyRune}
                visible={visibleAnimation}
                animationDuration={animationDuration}
                animationType='pulse'
                imageSize='small' />
            : <></>
        }

        <Header as='h2' icon style={{ margin: '1em' }}>
            {done
                ? <></>
                : <Icon name='dont' color='grey' />
            }

            {header}
            <Header.Subheader>
                {subheader1}
            </Header.Subheader>
            <Header.Subheader>
                {subheader2}
            </Header.Subheader>
        </Header>
    </>
)

export default ProgressSteps