import { createSlice } from '@reduxjs/toolkit';

export const slice = createSlice({
    name: 'accounts',
    initialState: {
        accounts: []
    },
    reducers: {
        setAccounts: (state, action) => {
            state.accounts = action.payload
        }
    }
})

export const { setAccounts } = slice.actions

export default slice.reducer;