import React from 'react'
import { Table, Rating } from 'semantic-ui-react'
import { useTranslation } from 'react-i18next'
import * as utils from '../../../utils'
import AccountTags from '../../../сomponents/AccountTags'
import config from '../../../config'

const fixedFloat = (value) => {
    if (!value) return value
    return Number(value.toFixed(2))
}

const MobileHistory = ({ ordersHistory, changeRating }) => {
    const { t } = useTranslation()

    const renderOrders = () => {
        return ordersHistory.map((order) => (
            <Table.Row key={order.orderId}>
                <Table.Cell>{order.mmr}</Table.Cell>
                <Table.Cell>{order.hours}</Table.Cell>
                <Table.Cell>
                    {
                        fixedFloat(order.amount)
                        ??
                        fixedFloat(order.hours * order.costPerHour)
                    }
                    $
                </Table.Cell>
                <Table.Cell>
                    <AccountTags
                        tags={utils.accountTagsFromString(order.tags)}
                        limit={3}
                        size='tiny' />
                </Table.Cell>
                <Table.Cell>
                    <Rating
                        icon='star'
                        defaultRating={order.reviewScore ?? 5}
                        maxRating={5}
                        onRate={(e, { rating, maxRating }) => {
                            changeRating(order.orderId, rating)
                        }}
                    />
                </Table.Cell>
            </Table.Row>
        ))
    }


    return (
        <>
            <Table striped color={config.mainColor} inverted textAlign='center' unstackable compact size='small'>
                <Table.Header>
                    <Table.Row>
                        <Table.HeaderCell>{t('orders-history.table.mmr')}</Table.HeaderCell>
                        <Table.HeaderCell>{t('orders-history.table.hours')}</Table.HeaderCell>
                        <Table.HeaderCell>{t('orders-history.table.amount')}</Table.HeaderCell>
                        <Table.HeaderCell>{t('orders-history.table.tags')}</Table.HeaderCell>
                        <Table.HeaderCell>{t('orders-history.table.reviewScore')}</Table.HeaderCell>
                    </Table.Row>
                </Table.Header>

                <Table.Body>
                    {renderOrders()}
                </Table.Body>
            </Table>
            {/* {ordersHistory} */}
        </>
    )
}

export default MobileHistory