import React, { useEffect, useState } from 'react'
import {
    Image,
    SemanticSTATICTRANSITIONS,
    SemanticSIZES,
    Transition,
} from 'semantic-ui-react'

interface AnimatedImageProps {
    src: string
    animationDuration: number
    animationType: SemanticSTATICTRANSITIONS
    imageSize: SemanticSIZES
    imageStyle?: React.CSSProperties
    toggleDutation: number
}
interface AnimatedImageState {
    v: boolean
}

const defaultProps = {
    toggleDutation: 2000
}
class AnimatedImage extends React.Component<AnimatedImageProps, AnimatedImageState> {
    constructor(props: AnimatedImageProps){
        super(props)
        this.state = {
            v: true
        }
    }

    componentWillUnmount() {
        console.log(this);
        
        clearInterval(this.intervalId)
    }

    componentDidMount() {
        console.log(this.props.toggleDutation || defaultProps.toggleDutation);

        let interval = setInterval(() => {
            this.setState({ v: !this.state.v })
        }, this.props.toggleDutation || defaultProps.toggleDutation)
    }

    render() {
        const {
            src,
            animationDuration,
            animationType,
            imageSize,
            imageStyle
        } = this.props
        return (
            <Transition
                animation={animationType}
                duration={animationDuration}
                visible={this.state.v}>
                <div><Image
                    style={imageStyle}
                    src={src}
                    size={imageSize}
                    centered />
                </div>
            </Transition>
        )
    }
}


export default AnimatedImage