import React, { useState } from "react";
import { useTranslation } from 'react-i18next';
import { Button, Divider, Header, Message } from "semantic-ui-react";

import '../../styles/ps.css'
import Order from "./Order";
import LoginRegisterForm from './LoginRegisterForm'
import OrdersHistory from './OrdersHistory'
import ResendEmailComponent from "./ResendEmail.jsx";
import { isMobile } from "react-device-detect";


const LogoutButton = ({ logout, t }) => {
    return (
        <div style={{ marginTop: '3em' }}>
            <Button
                onClick={logout}
                content={t('personal-space.logout-button')}
            />
        </div>
    )
}

const PSRender = ({ role, returnClientOrders, logout, resendEmail }) => {
    const { t } = useTranslation()

    const styleHeader = { position: 'relative', top: '2em', marginBottom: '4em' }
    const styleDiv = { marginBottom: '2em' }

    const ordersHistory = () => <OrdersHistory />

    switch (role) {
        case 'auth-not-confirmed':
            return (
                <div className='center' style={styleDiv}>
                    <Header content={t('personal-space.order-details')} style={styleHeader} />
                    <Header content={t('personal-space.eg')} />
                    <Order mmr={5800}
                        deadline={new Date().setHours(25)}
                        login={'login'} password={'password'} />
                    <div style={{ marginTop: '3em' }}>
                        {t('personal-space.email-not-veryfied')}
                    </div>
                    <ResendEmailComponent t={t} resendEmail={resendEmail} />
                    <LogoutButton t={t} logout={logout} />
                </div>
            )
        case 'auth-and-made-order':
            return (
                <div className='center' style={styleDiv}>
                    <div style={styleHeader}></div>
                    <Header content={t('personal-space.header-orders')} />
                    {returnClientOrders()}
                    {ordersHistory()}
                    <LogoutButton t={t} logout={logout} />
                </div>
            )
        case 'auth-not-made-order':
            return (
                <div className='center' style={styleDiv}>
                    <Header content={t('personal-space.order-details')} style={styleHeader} />
                    <Header content={t('personal-space.eg')} />
                    <Order mmr={5800}
                        deadline={new Date(new Date().setHours((new Date()).getHours() + 1))}
                        login={'login'} password={'password'} />
                    {ordersHistory()}
                    <LogoutButton t={t} logout={logout} />
                </div>
            )
        case 'unauth':
            return (
                <div className='center' style={styleDiv}>
                    <MessageBalance />
                    <LoginRegisterForm />
                    <Divider />
                    <Header content={t('personal-space.order-details')} style={styleHeader} />
                    <Header content={t('personal-space.eg')} />
                    <Order mmr={5800}
                        deadline={new Date().setHours(25)}
                        login={'login'} password={'password'} />
                    <br />
                </div>
            )
        default:
            return <div style={{ minHeight: '100vh', marginTop: '2em' }}></div>
    }
}

const MessageBalance = () => {
    const { t } = useTranslation()
    const [visible, setVisible] = useState(true)

    return (
        <>
            {visible
                ?
                <Message
                    color='orange'
                    header={t('personal-space.message.header')}
                    content={t('personal-space.message.content')}
                    onDismiss={() => setVisible(false)}
                    style={{
                        marginLeft: 'auto',
                        marginRight: 'auto',
                        width: isMobile ? '90%' : '50%',
                        marginBottom: "8em",
                        marginTop: isMobile ? "-20%" : "-4em",
                    }}
                />
                : null}
        </>
    )
}

export default PSRender