import React from "react";
import config from "../../../../config";
import { Grid } from "semantic-ui-react";
import { Link } from "react-router-dom";


const PayForRentErrorMsg = ({
    error, errorCode, errorBottom, isLinkOnBottom
}) => {
    const linkTo = errorCode === 402 ?
        config.pages.balance : config.pages.personalSpace
    if (error) {
        return (
            <Grid.Row>
                <Grid.Column textAlign="center">
                    <div style={{ marginBottom: '1em' }}>
                        {error}
                    </div>
                    <div style={{ marginTop: '3px' }}>
                        {
                            isLinkOnBottom
                                ? <Link to={linkTo}>{errorBottom}</Link>
                                : <>{errorBottom}</>
                        }
                    </div>
                </Grid.Column>
            </Grid.Row>
        )
    }
    else return null
}

export default PayForRentErrorMsg