import React, { useEffect, useState } from "react"
import { useTranslation } from "react-i18next"
import {
    Button,
    Header,
    Input,
    Segment,
} from "semantic-ui-react"
import * as api from '../../api/index'
import './balance.css'
import { isMobile } from 'react-device-detect'
import ProgressSteps from "./ProgressSteps.jsx"
import BestAccounts from "../Home_Page/desktop/BestAccounts"
import MobileBestAccounts from '../Home_Page/mobile/BestAccounts.jsx'
import ReplenishmentHistory from './ReplenishmentHistory'
import config from "../../config"
import ReactGA from 'react-ga'
import { replenishmentType } from './_types'


const Balance: React.FC<JSX.Element> = () => {
    const { t } = useTranslation()
    const [balance, setBalance] = useState<number>(0)
    const [amount, setAmount] = useState<string>('')
    const [error, setError] = useState<string>('')
    const [replenishments, setReplenishments] = useState<replenishmentType[]>([])

    useEffect(() => {
        ReactGA.pageview(window.location.hash.slice(1))

        // Get Balance
        const token = localStorage.getItem('token')
        api.getBalance(token, (err: any, res: false | { data: number }) => {
            if (err) throw err
            if (!(res && res.data)) return

            if (res) setBalance(+(res.data).toFixed(2))
        })

        // Get replenishments history
        api.getReplenishmentsHistory(token, (err: any, res: false | { data: replenishmentType[] }) => {
            if (err) throw err
            if (!(res && res.data)) return
            setReplenishments(res.data)
        })
    }, [])

    useEffect(() => {
        if (parseFloat(amount) < 1 && parseFloat(amount) !== 0)
            setError(t('balance.min-sum-error'))
        else setError('')
    }, [amount])

    const addFundsClicked = () => {
        const token = localStorage.getItem('token')
        const region: string | null = localStorage.getItem('i18nextLng')
        const intamount = parseFloat(amount)
        if (intamount < 1) return

        api.addFunds(token, { amount: intamount, region }, (err: any, result_url: string) => {
            if (err?.response?.status === 403)
                setError(t('balance.errors.notLoggedIn'))
            if (err) throw err
            if (result_url) window.open(result_url, "_self")
        })
    }

    const desktopCase = () => {
        return (
            <Input
                onChange={(e) => setAmount(e.target.value)}
                value={amount}
                action={
                    <Button
                        positive
                        icon='money'
                        labelPosition='right'
                        content={t('balance.button-add-funds')}
                        onClick={addFundsClicked}
                    />
                }
                placeholder={t('balance.input-placeholder')}
            />
        )
    }

    const mobileCase = () => {
        return (
            <>
                <Input
                    onChange={(e) => setAmount(e.target.value)}
                    value={amount}
                    placeholder={t('balance.input-placeholder')}
                />
                <Button
                    positive
                    style={{
                        marginTop: '2vh'
                    }}
                    icon='money'
                    labelPosition='right'
                    content={t('balance.button-add-funds')}
                    onClick={addFundsClicked}
                />
            </>
        )
    }

    const getBalance = () => balance === 0 ? '0.00' : balance + ' $'

    return (
        <div id='balance' >
            <Segment raised color={config.mainColor} compact
                style={{
                    display: 'block',
                    marginLeft: 'auto',
                    marginRight: 'auto',
                    width: isMobile ? '90vw' : '45%',
                    marginTop: isMobile ? '5vh' : '2em'
                }
                }>
                <Header textAlign="center" >
                    {t('balance.header')} {getBalance()}
                </Header>
                {
                    isMobile ?
                        mobileCase()
                        :
                        desktopCase()
                }
                <div style={{ textAlign: 'center' }}>
                    {error}
                </div>
                {replenishments.length === 0 ? null :
                    <ReplenishmentHistory replenishments={replenishments} />}
            </Segment>
            < ProgressSteps />
            {
                isMobile
                    ? <div>
                        < MobileBestAccounts />
                    </div>
                    : <div>
                        <BestAccounts />
                    </div>
            }
            <br />
            <br />
        </div>
    )
}

export default Balance