
export const accountTagsFromString = (tags) =>
    tags.map(tag => {
        switch (tag) {
            case 'Dota+':
                return { color: 'yellow', name: 'Dota+' }
            case 'Vip':
                return { color: 'purple', name: 'Vip' }
            case 'Low MMR':
                return { color: 'brown', name: 'Low MMR' }
            case 'WTF Price':
                return { color: 'red', name: 'WTF Price' }
            case 'Cool Items':
                return { color: 'pink', name: 'Cool Items' }
            case 'High MMR':
                return { color: 'blue', name: 'High MMR' }
            case 'Battlepass':
                return { color: 'teal', name: 'Battlepass' }
            default:
                return { name: "", color: 'grey' }
        }
    })


/**
 * Build a query str, if no tags return ''
 * @param {Array} startTags Array of tags
 * @returns {string} tag1:tag2 (tags splitted by colon)
 */
export const getTagList = (startTags) => {
    if (startTags.length === 0) return ''
    const tags = []
    Object.entries(startTags).forEach(tag => {
        const tagValue = tag[0]
        const isTagSelected = tag[1]
        if (isTagSelected) tags.push(tagValue)
    })

    let queryTags = ''
    if (!tags || tags === '') return ''
    tags.forEach(tag => {
        queryTags += `:${tag}`
    })
    queryTags = queryTags.replace(':', '')
    return queryTags
}

