import React, { useEffect, useState } from "react";
import { useDispatch } from "react-redux";
import { setAccounts } from "../../../redux/accountsSlice";
import * as api from '../../../api/index'
import * as utils from '../../../utils'
import DesktopSearch from './DesktopSearchForAccount.jsx'
import MobileSearch from './MobileSearchForAccount'
import useDebouncedEffect from '../../../utils/useDebouncedEffect'
import config from "../../../config";
import { isMobile } from "react-device-detect";

const SearchForAccount = () => {
    const dispatch = useDispatch()
    const [minMmr, setMinMmr] = useState(config.availableMMRs.min)
    const [maxMmr, setMaxMmr] = useState(config.availableMMRs.max)
    const [maxPriceUSD, setMaxPrice] = useState(config.prices.accountsMax)
    const [tags, setTags] = useState([])
    const [visible, setVisible] = useState(false)

    const findAccounts = () => {
        api.getAccountsWithOptions({
            minMmr,
            maxMmr,
            maxPriceInUSD: maxPriceUSD,
            tags: utils.getTagList(tags),
            region: localStorage.getItem('i18nextLng')
        }, (err, res) => {
            if (err) return console.error('getAccountsWithOptions error:', err)
            if (res) dispatch(setAccounts(res.data))
        })
    }

    useEffect(() => {
        setVisible(true)
    }, [])

    useDebouncedEffect(
        findAccounts,
        [minMmr, maxMmr, maxPriceUSD, tags],
        300);

    const priceToRender = () => parseFloat(maxPriceUSD).toFixed(1)

    return !isMobile
        ? <DesktopSearch
            setMinMmr={setMinMmr}
            setMaxMmr={setMaxMmr}
            priceToRender={priceToRender}
            maxPriceUSD={maxPriceUSD}
            setMaxPrice={setMaxPrice}
            setTags={setTags}
            visible={visible} />
        : <MobileSearch setTags={setTags} />
}

export default SearchForAccount