import React, { useEffect, useState } from "react";
import { Card, Header, Segment } from "semantic-ui-react";
import { useTranslation } from 'react-i18next';
import { useSelector, useDispatch } from "react-redux";
import ReactGA from 'react-ga'

import './accounts.css'
import SearchForAccount from './SearchForAccount'
import * as api from '../../api/index'
import AccountCard from "./AccountCard";
import { setAccounts } from "../../redux/accountsSlice";
import { isMobile } from 'react-device-detect';
import config from "../../config";


const Accounts = () => {
    const dispatch = useDispatch()
    const { t } = useTranslation()
    const accounts = useSelector(state => state.accounts.accounts)
    const [busyAccs, setBusyAccs] = useState([])

    useEffect(() => {
        ReactGA.pageview(window.location.hash.slice(1))
    }, [])

    useEffect(() => {
        api.getBusyAccounts((err, res) => {
            if (err) throw err
            console.log(res.data);
            if (res && res.data)
                setBusyAccs(res.data)
        })
    }, [])

    const renderCards = () =>
        accounts.map((account, index) =>
            <AccountCard
                _id={account._id}
                key={`account${index}`}
                rating={account.rating}
                behavior={account.behavior}
                trophyLevel={account.trophyLevel}
                costPerHour={account.costPerHour}
                items={account.items}
                tags={account.tags}
                accountTagsAvailable={true}
                prices={account.prices}
            />
        )

    const renderBusyAccounts = () =>
        busyAccs.map((account, index) =>
            <AccountCard
                _id={account._id}
                key={`account${index}`}
                rating={account.rating}
                behavior={account.behavior}
                trophyLevel={account.trophyLevel}
                costPerHour={account.costPerHour}
                items={account.items}
                tags={account.tags}
                accountTagsAvailable={true}
                prices={account.prices}
                busyAccount={true}
            />
        )

    return (
        <div>
            <Segment secondary color={config.mainColor} raised
                style={{
                    width: isMobile ? '90%' : '60%',
                    marginLeft: isMobile ? '5%' : '20%',
                    marginTop: '1em'
                }}>
                <SearchForAccount />
            </Segment>
            <Header textAlign='center' style={{ marginTop: '2em' }}
                content={t('accounts.accounts-list')} />
            <Segment secondary color={config.mainColor} raised
                style={{
                    width: isMobile ? '90%' : '60%',
                    marginLeft: isMobile ? '5%' : '20%',
                    marginBottom: '2em'
                }}>
                <Card.Group centered>
                    {renderCards()}
                </Card.Group>
            </Segment>
            {/* TODO: let user see after what time account will be able */}
            {busyAccs.length !== 0 ?
                <>
                    <Header
                        content={t('accounts.busy-accounts')}
                        textAlign='center'
                    />
                    <Segment secondary color={config.mainColor} raised
                        style={{
                            width: isMobile ? '90%' : '60%',
                            marginLeft: isMobile ? '5%' : '20%',
                            marginTop: '1em'
                        }}>
                        <Card.Group centered>
                            {renderBusyAccounts()}
                        </Card.Group>
                    </Segment>
                </>
                : null
            }
        </div>
    )
}

export default Accounts