import React from "react";
import { Card, Header } from 'semantic-ui-react'

import ItemCard from "./ItemCard.jsx"


const BigItemsBox = ({ items }) => {
    const getItems = () => {
        const array = []
        items.forEach(item => {
            array.push(
                <ItemCard item={item} marign={20} />
            )
        });
        return array
    }

    return (
        <div>
            <Header content='Items list' textAlign="center" />
            <Card.Group centered itemsPerRow={4} doubling >
                {getItems()}
            </Card.Group>
            <br />
        </div>
    )
}

export default BigItemsBox