import React, { useEffect, useState } from "react";
import { Header, Loader } from "semantic-ui-react";
import { useTranslation } from 'react-i18next';
import BalanceGifted from './BalanceGifted.jsx'
import * as api from '../../api/'
import { useDispatch } from "react-redux";

const CondirmPage = () => {
    const dispatch = useDispatch()
    const { t } = useTranslation()
    const [role, setRole] = useState('unconf')
    useEffect(() => {
        let code = window.location.href.split('=')[1];
        api.confirmEmail(code, (res, err) => {
            console.log(res);
            switch (res.data) {
                case 'email just confirmed':
                    setRole('just-confirmed')
                    // dispatch(setRole('auth-not-made-order'))
                    break;
                case 'email already confirmed':
                    setRole('already-confirmed')
                    break;
                default:
                    break;
            }
        })
    }, [])
    switch (role) {
        case 'already-confirmed':
            return (
                <div style={{ minHeight: '95vh', textAlign: 'center' }}>
                    <Header style={{ marginTop: '3em' }} content={t('confirm-page.already-confirmed')} />
                </div>
            )
        case 'just-confirmed':
            return (
                <div style={{ minHeight: '95vh', textAlign: 'center' }}>
                    <BalanceGifted />
                </div>
            )
        default:
            return (
                <div style={{ minHeight: '95vh', textAlign: 'center' }}>
                    <Loader style={{ marginTop: '3em' }} active inline >
                        {t('confirm-page.loading')}
                    </Loader>
                </div>
            )
    }
}

export default CondirmPage