import React, { useState, useEffect } from "react";
import { Grid } from "semantic-ui-react";
import AccountCard from "../../Accounts/AccountCard";
import * as api from '../../../api/index'

const BestAccountsGrid = () => {
    const [bestAccounts, setBestAccounts] = useState([])

    useEffect(() => {
        api.getBestAccounts((err, accounts) => {
            if (err) throw err
            console.log('accounts', accounts);
            setBestAccounts(accounts)
        })
    }, [])

    const renderBestAccs = () => {
        return bestAccounts.map((account) => {
            const {
                rating,
                behavior,
                trophyLevel,
                _id,
                costPerHour,
                items,
            } = account

            return (
                <AccountCard
                    _id={_id}
                    rating={rating}
                    behavior={behavior}
                    costPerHour={costPerHour}
                    trophyLevel={trophyLevel}
                    items={items}
                    tags={account.tags}
                    accountTagsAvailable={false}
                />
            )
        })
    }

    return (
        <div style={{ textAlign: 'center' }}>
            {renderBestAccs()}
        </div>
    )
}

export default BestAccountsGrid