import React, { useState, useEffect } from "react";
import { useTranslation } from 'react-i18next';
import '../../../../styles/payment_page.css'
import '../../input.css'
import config from "../../../../config";
import * as api from '../../../../api/index'
import { isMobile } from 'react-device-detect'
import PayForRentDesktop from "./PayForRentDesktop.jsx";
import PayForRentMobile from "./PayForRentMobile.jsx";


const PayForRent = ({ _id, costPerHour, trophyLevel, behavior, rating, prices }) => {
    const [error, setError] = useState('')
    const [errorBottom, setErrorBottom] = useState('')
    const [errorCode, setErrorCode] = useState(0)
    const [loading, setLoading] = useState(false)
    const { t } = useTranslation()
    const [hoursAmount, setHoursAmount] = useState(config.availableHours.min)
    const [promo, setPromo] = useState('')
    const [coef, setCoef] = useState(1)

    const srcScrProfile = config.url + `/account-card/${_id}_1.jpg`
    const srcScrMmr = config.url + `/account-card/${_id}_3.jpg`

    useEffect(() => {
        if (promo.length < 5) return
        api.getPromoCoef(promo, (err, res) => {
            if (err) {
                console.log(err);
                setCoef(1)
            } else
                setCoef(res)
        })
    }, [promo])

    const makePaymentRequest = () => {
        setLoading(true)
        api.makeOrder(_id, hoursAmount,
            localStorage.getItem('i18nextLng'),
            localStorage.getItem('token'),
            promo,
            (err) => {
                if (err) {
                    console.log('err.response', err.response)
                    if (err.response.data === 'Forbidden') {
                        setLoading(false)
                        setErrorCode(403)
                        setError(t('alertMessages.cant_make_order'))
                        setErrorBottom(t('alertMessages.link_to_ps'))
                    } else if (err.response.data === 'Payment Required') {
                        setLoading(false)
                        setErrorCode(402)
                        setError(t('alertMessages.not_enough_money'))
                        setErrorBottom(t('alertMessages.link_to_add_money'))
                    } else if (err.response.data === 'Not Acceptable') {
                        setLoading(false)
                        setErrorCode(406)
                        setError(t('alertMessages.account_not_active'))
                        setErrorBottom(t('alertMessages.pls_activate_email'))
                    } else if (err.response.data === 'account not available'){
                        setLoading(false)
                        setErrorCode(403)
                        setError(t('alertMessages.account_unavailable'))
                    }
                } else
                    window.open(config.frontend_url + config.pages.personalSpace, "_self")
            })
    }

    const sum = Math.round(coef * costPerHour * hoursAmount * 1000) / 1000

    const properties = {
        costPerHour, trophyLevel, behavior, rating, makePaymentRequest,
        promo, setPromo, hoursAmount, setHoursAmount, coef, setCoef,
        srcScrProfile, srcScrMmr, prices,
        loading, error, errorCode, errorBottom
    }


    return (
        <>
            {
                isMobile
                    ? <PayForRentMobile data={properties} />
                    : <PayForRentDesktop data={properties} />
            }
        </>
    )

}

export default PayForRent