import React, { useState, useEffect } from 'react'
import * as api from '../../../api/index'
import { Header } from 'semantic-ui-react'
import { useTranslation } from 'react-i18next'
import { isMobile } from 'react-device-detect'
import MobileHistory from './MobileHistory.jsx'
import DesktopHistory from './DesktopHistory.jsx'


const OrdersHistory = () => {
    const { t } = useTranslation()
    const [ordersHistory, setOrdersHistory] = useState([])

    useEffect(() => {
        const token = localStorage.getItem('token')
        if (!token) return console.log('No token found. Cannot get orders history');

        api.getOrdersHistory(token, (err, resOrdersHistory) => {
            if (err) return console.log('Error getting orders history:', err);
            setOrdersHistory(resOrdersHistory)
        })
    }, [])

    const changeRating = (orderId, rating) => {
        console.log(`Changing rating of ${orderId} to ${rating}`);
        const token = localStorage.getItem('token')
        if (!token) return console.log('Cannot change rating, no token found in LS');
        api.changeOrderRating(token, { orderId, rating })
    }

    const mobileStyle = { marginTop: '2em', marginLeft: '0.5em', marginRight: '0.5em' }
    const desktopStyle = { marginTop: '2em', width: '70%', marginLeft: 'auto', marginRight: 'auto' }

    return (
        <div>
            {ordersHistory && ordersHistory.length !== 0
                ? <div style={isMobile ? mobileStyle : desktopStyle}>
                    <Header>{t('')}</Header>
                    {isMobile
                        ? <MobileHistory ordersHistory={ordersHistory} changeRating={changeRating} />
                        : <DesktopHistory ordersHistory={ordersHistory} changeRating={changeRating} />}
                </div>
                : <></>
            }
        </div>
    )
}

export default OrdersHistory