const BalanceGifted = () => {
    const { t } = useTranslation()

    const mobileStyle = { margin: "3em", width: '90%', marginLeft: 'auto', marginRight: 'auto' }
    const desktopStyle = { margin: "3em", width: '50%', marginLeft: 'auto', marginRight: 'auto' }
    return (
        <>
            <Segment style={isMobile ? mobileStyle : desktopStyle} raised>
                <Header as='h2' icon>
                    <Icon color='purple' name='gift' />
                    {t('confirm-page.balance-gifted.header')}
                    <Header.Subheader>
                        {t('confirm-page.balance-gifted.subheader')}
                    </Header.Subheader>
                </Header>
            </Segment>
        </>
    )
}

export default BalanceGifted