import React, { useEffect } from "react";
import { Card, Modal, Image, Popup, Button } from 'semantic-ui-react'
import { useTranslation } from 'react-i18next';
import PopupItems from './PopupItems'
import BigItemsBox from './BigItemsBox.jsx'
import AccountTags from '../../../сomponents/AccountTags.jsx'
import { isMobile } from "react-device-detect";
import PayForRent from './PayForRent'
import config from "../../../config";


const AccountCard = ({
    itemsOpen, setItemsOpen, rating,
    items, behavior, costPerHour, tags,
    tagsObjects, pathToAccountImage,
    trophyLevel, _id, haveItems,
    setPayForRentOpen, payForRentOpen,
    accountTagsAvailable, prices, busyAccount = false
}) => {
    const isMobileHomePage = isMobile && window.location.href.split('#')[1] === '/'
    const idForEvents = `${_id}_eventListener`
    const { t } = useTranslation()

    useEffect(() => {
        if (!busyAccount) {
            const openMenuOnClick = () => { setPayForRentOpen(true) }
            const cardImg = document.getElementById(idForEvents)
            cardImg.addEventListener('click', openMenuOnClick)
            return () => {
                cardImg.removeEventListener('click', openMenuOnClick)
            }
        }
    }, [])

    const cardStyle = isMobileHomePage ? {
        marginLeft: 'auto',
        marginRight: 'auto',
        marginTop: '10px',
        marginBottom: '40px',
    } : { margin: '32px' }

    const renderWithItems = () => {
        return (
            <div className='grid-card'>
                <div className="box" style={{ fontSize: "115%" }}>
                    {t('accounts.card.rating')}
                    <div>{rating}</div>
                </div>
                <Modal
                    onClose={() => setItemsOpen(false)}
                    onOpen={() => setItemsOpen(true)}
                    open={itemsOpen}
                    content={<BigItemsBox items={items} />}
                />
                <div className="box">
                    <Popup
                        trigger={
                            <Button className="box" content={t('accounts.card.items')}
                                compact onClick={() => setItemsOpen(!itemsOpen)} />
                        }
                        content={
                            <PopupItems items={items} />
                        } />
                </div>
            </div>
        )
    }

    const renderWithonutItems = () => {
        return (
            <div className="center" style={{
                marginTop: '12px',
                marginBottom: '12px',
            }}>
                {t('accounts.card.rating')} {rating}
            </div>
        )
    }

    const accTagsDivStyle = { textAlign: 'center', margin: '0.3em', marginTop: '.1em' }
    const accTagsStyle = { margin: '.2em' }

    return (
        <Card style={cardStyle} className='account-card' color={config.mainColor}>
            <Image
                src={pathToAccountImage}
                id={idForEvents}
                style={busyAccount ? {} : { cursor: 'pointer' }}
            />
            <div className='grid-card'>
                <div className="box">{t('accounts.card.behavior')}</div>
                <div className="box">{t('accounts.card.price')}</div>
                <div className="box">{behavior}</div>
                <div className="box">{costPerHour}{t('currency')} {t('accounts.card.per-hour')}</div>
            </div>

            {haveItems ? renderWithItems() : renderWithonutItems()}

            <Modal
                onClose={() => setPayForRentOpen(false)}
                onOpen={() => setPayForRentOpen(true)}
                open={payForRentOpen}
                content={
                    <PayForRent
                        rating={rating} behavior={behavior} _id={_id}
                        trophyLevel={trophyLevel} costPerHour={costPerHour}
                        prices={prices}
                    />
                }
            />

            {
                tags && tags.length !== 0
                    ?
                    <>
                        <div>
                            <AccountTags accountTagsAvailable={accountTagsAvailable}
                                tags={tagsObjects} style={accTagsDivStyle} tagsStyle={accTagsStyle} />
                        </div>
                    </>
                    : <div>
                        <AccountTags tags={[{ name: 'Default', color: 'grey' }]}
                            style={accTagsDivStyle} />
                    </div>

            }
        </Card >
    )
}

export default AccountCard