import React from "react";

class Timer extends React.Component {
    constructor(props) {
        super(props);
        this.state = { count: 0 }
    }
    //props.timeLeft is time in sec

    componentWillUnmount() {
        clearInterval(this.interval)
    }
    componentDidMount() {
        if (this.props.timeLeft<0) {
            this.setState({count: this.props.timeLeft})
            return null
        }
        let interval = setInterval(()=>{
            let timeLeft = this.props.timeLeft - this.state.count
            if (timeLeft-1<0) clearInterval(interval)
            else this.setState({count: this.state.count+1})
        },1000)
    }
    addZero(time) {
        if (time<10)
            return '0'
    }
    hours (timeLeft) {
        return Math.trunc(timeLeft / 3600) 
    }
    minutes (timeLeft) {
        let min = Math.trunc(timeLeft / 60) % 60
        return min<10? '0'+min : min
    }
    seconds(timeLeft) {
        let sec = Math.trunc(timeLeft % 60)
        return sec<10? '0'+sec : sec
    }
    render() {
        let timeLeft = this.props.timeLeft - this.state.count
        let timer = <span>
            {this.hours(timeLeft)}:
            {this.minutes(timeLeft)}:
            {this.seconds(timeLeft)}
        </span>
        return (<span>{timer}</span>)
    }
}

export default Timer