import React, { useState } from "react";
import { useTranslation } from 'react-i18next';

import CPRender from "./CPRender";
import * as api from '../../api/'
import config from "../../config";

const ChangePassword = () => {
    const { t } = useTranslation()
    const [newPassword, setNewPassword] = useState('')
    const [confirmNewPassword, setConfirmNewPassword] = useState('')
    const [error, setError] = useState(false)
    const [errorText, setErrorText] = useState('')
    const [isLoading, setIsLoading] = useState(false)
    const [isCheckmarkHidden, setIsCheckmarkHidden] = useState(true)

    let code; let email
    if (!window.location.href.split('?')[1]) return null
    for (let key_val of window.location.href.split('?')[1].split('&')) {
        switch (key_val.split('=')[0]) {
            case 'changePasswordCode':
                code = key_val.split('=')[1]
                break
            case 'email':
                email = key_val.split('=')[1]
                break
            default: break
        }
    }
    const changePassword = () => {
        if (validatePasswords()) {
            setIsLoading(true)
            api.changePassword({ email, newPassword, changePasswordCode: code }, (err, res) => {
                if (err) throw err
                setIsLoading(false)
                setIsCheckmarkHidden(false)
                setTimeout(() => { window.open(config.frontend_url + config.pages.personalSpace, "_self") }, 2000)
            })
        }
    }
    const validatePasswords = () => {
        if (newPassword.length < 5 || confirmNewPassword.length < 5) {
            setError(true)
            setErrorText(t('change-password.not-enough-symbols'))
            return false
        } else {
            setError(false)
        }
        if (newPassword === confirmNewPassword) {
            setError(false)
            return true
        }
        else {
            setError(true)
            setErrorText(t('change-password.mismatch'))
            return false
        }
    }

    return <CPRender newPassword={newPassword} setNewPassword={setNewPassword} error={error}
        confirmNewPassword={confirmNewPassword} setConfirmNewPassword={setConfirmNewPassword}
        validatePasswords={validatePasswords} setError={setError} errorText={errorText} t={t}
        changePassword={changePassword} isLoading={isLoading} isCheckmarkHidden={isCheckmarkHidden} />
}

export default ChangePassword