import React from "react";
import {
    Header,
    Container,
    Label,
    Button,
} from 'semantic-ui-react'
import config from "../../../config";
import { Link } from "react-router-dom";
import { useTranslation } from "react-i18next";


const Slider = ({
    textIsLeft, back, image, visible, text_header1,
    text_header2, fist_text, second_text, thrid_text,
    comingSoon
}) => {
    const { t } = useTranslation()

    const duration = '1250'
    const buttonDuration = '1750'
    const button_text = comingSoon ? 'Soon!' : t('home.begin-button-text')

    const styleWrapper = (background) => {
        return {
            backgroundImage: `url(${background})`,
            backgroundSize: 'cover',
            backgroundRepeat: 'no-repeat',
            backgroundPosition: 'center',
            // height: '60vh',
            padding: '3vh',
            minHeight: '36vh'
        }
    }

    const styleDiv = {
        color: '#f5f5dc',
        position: 'relative',
        top: '35%',
        fontSize: '150%',
    }
    const buttonStyle = { marginTop: '1vh' }
    const liStyle = { marginTop: '.4vh', color:'wheat'  }

    return (
        <div style={styleWrapper(back)}>
                <div style={{ marginLeft: '1em' }}>
                    <Header
                        as='h1'
                        style={{ color: '#f5f5dc', fontSize: '1.3em' }}
                        content={`${text_header1} ${text_header2}`}
                    />
                    <li style={liStyle}>{fist_text}</li>
                    <li style={liStyle}> {second_text}</li>
                    <li style={liStyle}>{thrid_text}</li>
                </div>
                <Container textAlign='center'>
                    {
                        comingSoon
                            ? <Label
                                style={buttonStyle}
                                content={t('home.soon-label-text')}
                                color={config.mainColor}
                                size='huge'
                            />
                            : <Link to={config.pages.accounts}>
                                <Button
                                    style={buttonStyle}
                                    color={config.mainColor}
                                    size='large'
                                    content={button_text}
                                />
                            </Link>
                    }
                </Container>
        </div>
    )
}

export default Slider